.get {
  // .twig__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .twig__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 183px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .twig__bottom

  &__bottom {
  }

  // .twig__cards

  &__cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      width: 723px;
      height: 206px;
      bottom: 0;
      left: 19%;
      border-radius: 10px;
      background: #f8f8f8;
      z-index: -1;
    }
    &::before {
      content: '';
      position: absolute;
      right: 24%;
      border-radius: 10px;
      border: 1px solid rgba(0, 174, 239, 0.49);
      width: 441px;
      height: 343px;
      top: -25%;
    }
  }

  // .twig__card

  &__card {
    padding: 25px 20px;
    display: flex;
    width: 260px;
    min-height: 364px;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    border: 4px solid #ddf3fd;
    background: #fff;
    position: relative;
    @media (max-width: 1600px) {
      min-height: 340px;
    }
    @media (max-width: 1400px) {
      width: 250px;
    }
    @media (max-width: 1300px) {
      width: 200px;
      padding: 10px;
      min-height: 280px;
    }
  }

  &__card-num {
    position: absolute;
    left: -20%;
    top: -16%;
    @media (max-width: 1600px) {
      width: 50px;
      height: auto;
      left: -3%;
      top: -4%;
    }
  }

  // .twig__card-top

  &__card-top {
    img {
      width: 70px;
      height: 70px;
      @media (max-width: 1300px) {
        width: 50px;
        height: 50px;
      }
    }
  }

  // .twig__card-center

  &__card-center {
    color: #007bb3;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    @media (max-width: 1600px) {
      font-size: 16px;
    }
    @media (max-width: 1300px) {
      font-size: 14px;
    }
  }

  // .twig__card-bottom

  &__card-bottom {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    line-height: 150%;
    @media (max-width: 1600px) {
      font-size: 14px;
    }
    @media (max-width: 1300px) {
      font-size: 12px;
    }
  }
}
