.callbook {
  // .callbook__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .callbook__body

  &__body {
    padding-top: 80px;
    display: flex;
    flex-direction: column;
    gap: 98px;
    padding-bottom: 50px;
  }

  // .callbook__bottom

  &__bottom {
    padding: 0 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1600px) {
      padding: 0;
    }
    @media (max-width: 1400px) {
      gap: 100px;
    }
  }

  // .callbook__left

  &__left {
  }

  // .callbook__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 106px;
    span {
      color: #000;
      font-family: Inter;
      font-size: 24px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%;
      text-transform: lowercase;
    }
    @media (max-width: 1600px) {
      gap: 50px;
    }
  }

  // .callbook__card

  &__card {
    display: flex;
    width: 304px;
    min-height: 304px;
    padding: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    img {
      width: 61.818px;
      height: 70px;
    }
    @media (max-width: 1600px) {
      width: 250px;
      height: 250px;
    }
  }

  // .callbook__title

  &__title {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 24px;
    font-weight: 500;
    line-height: 150%;
    text-transform: uppercase;
  }

  // .callbook__card-bottom

  &__card-bottom {
    display: flex;
    justify-content: center;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    max-width: 290px;
    margin: 24px auto;
    padding: 20px;
    color: #007bb3;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    text-transform: uppercase;
    position: relative;
    &::after {
      content: '';
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      position: absolute;
      border-radius: 10px;
      background: #ddf3fd;
      width: 476px;
      height: 191px;
      z-index: -1;
    }
  }

  // .callbook__right

  &__right {
  }
}
