.analitic {
  // .analitic__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .analitic__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 108px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .analitic__bottom

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 86px;
    position: relative;
    @media (max-width: 1720px) {
      justify-content: center;
      gap: 50px;
    }
  }

  // .analitic__column

  &__column {
    &:nth-of-type(2) {
      padding-left: 420px;
      @media (max-width: 1720px) {
        padding-left: unset;
      }
    }
    @media (max-width: 1720px) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  // .analitic__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 350px;
    @media (max-width: 1720px) {
      gap: 100px;
    }
  }

  // .analitic__card

  &__card {
    display: flex;
    width: 494px;
    min-height: 172px;
    padding: 12px 20px;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    position: relative;
  }

  &__line1 {
    width: 178px;
    height: 193px;
    border-bottom: 4px dashed #95dbfa;
    border-left: 4px dashed #95dbfa;
    position: absolute;
    bottom: -120%;
    left: 50%;
    @media (max-width: 1720px) {
      display: none;
    }
  }
  &__line2 {
    width: 178px;
    height: 193px;
    border-top: 4px dashed #95dbfa;
    border-left: 4px dashed #95dbfa;
    position: absolute;
    top: -120%;
    left: 50%;
    @media (max-width: 1720px) {
      display: none;
    }
  }
  &__line3 {
    width: 178px;
    height: 193px;
    border-top: 4px dashed #95dbfa;
    border-right: 4px dashed #95dbfa;
    position: absolute;
    right: -37%;
    top: 50%;
    @media (max-width: 1720px) {
      display: none;
    }
  }
  &__square1 {
    width: 195px;
    height: 195px;
    border-radius: 10px;
    border: 8px solid #ddf3fd;
    background: #ddf3fd;
    position: absolute;
    bottom: 0;
    left: 10%;
  }
  &__square2 {
    width: 195px;
    height: 195px;
    border-radius: 10px;
    border: 8px solid #f8f8f8;
    background: #f8f8f8;
    position: absolute;
    top: 5%;
    right: 57%;
  }
  &__square3 {
    width: 195px;
    height: 195px;
    border-radius: 10px;
    border: 8px solid #ddf3fd;
    background: #ddf3fd;
    position: absolute;
    top: 5%;
    right: 13%;
  }
  &__square4 {
    width: 195px;
    height: 195px;
    border-radius: 10px;
    border: 1px solid #82d7f7;
    position: absolute;
    right: 34%;
    bottom: 0;
  }

  // .analitic__left

  &__left {
    img {
      width: 70px;
      height: 70px;
    }
  }

  // .analitic__right

  &__right {
    color: #353535;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 244px;
  }
}
