.detailing-second {
  &__container {
    width: 100%;
    max-width: 1920px;
    padding: 0;
  }
  &__items {
    align-items: center;
  }
  &__center {
    width: fit-content;
    margin-top: 0;
    img {
      width: 1852px;
      height: 1080px;
      @media (max-width: 1720px) {
        width: unset;
        height: unset;
      }
    }
  }
  &__right {
    position: relative;
    top: 80px;
  }
  &__right-list-wrapper {
    span {
      font-weight: 700;
    }
  }
  &__right-line1 {
    position: absolute;
    left: -31%;
    top: 32%;
    @media (max-width: 1720px) {
      left: -25%;
      svg {
        width: 80px;
      }
    }
    @media (max-width: 1600px) {
      left: -25%;
      svg {
        width: 73px;
      }
    }
    @media (max-width: 1400px) {
      left: -24%;
      svg {
        width: 69px;
      }
    }
    @media (max-width: 1300px) {
      left: -20%;
      svg {
        width: 56px;
      }
    }
  }
  &__list-item {
    align-items: start;
  }
}

.detailing-flex__items {
  gap: 3px;
  justify-content: unset;
}
