.autocall {
  padding: 45px 0;
  // .autocall__auto

  &__auto {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  &__left {
    @media (max-width: 1720px) {
      align-items: center;
    }
  }
  &__right {
    @media (max-width: 1720px) {
      padding: 50px;
    }
  }

  // .autocall__auto-title

  &__auto-title {
    color: #353535;
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    text-align: center;
    @media (max-width: 1400px) {
      font-size: 23px;
    }
  }

  // .autocall__auto-cards

  &__auto-cards {
    display: flex;
    align-items: center;
    gap: 40px;
    justify-content: center;
    @media (max-width: 1720px) {
      gap: 19px;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }
  &__body {
    padding-top: 92px;
  }

  &__text {
    max-width: 869px;
    p {
      color: #353535;
      font-family: Inter;
      font-size: 24px;
      font-style: normal;
      font-weight: 300;
      line-height: 150%;
      @media (max-width: 1400px) {
        font-size: 20px;
      }
    }
    @media (max-width: 1720px) {
      max-width: 665px;
    }
  }

  // .autocall__auto-card

  &__auto-card {
    color: #007bb3;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%;
    padding: 23px 44px;
    border-radius: 10px;
    border: 1px solid #ddf3fd;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    text-transform: uppercase;
    @media (max-width: 1720px) {
      font-size: 20px;
    }
    @media (max-width: 1400px) {
      font-size: 16px;
      padding: 15px 30px;
    }
  }

  // .autocall__text-subtitle

  &__text-subtitle {
  }
}
