.monitoring-one {
  padding-top: 107px;
  // .monitoring-one__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
  }

  // .monitoring-one__title

  &__title {
  }

  // .monitoring-one__items

  &__items {
    display: flex;
    position: relative;
    margin-top: -40px;
    gap: 24px;
    margin-bottom: 20px;
    @media (max-width: 1720px) {
      gap: 13px;
    }
  }

  // .monitoring-one__left

  &__left {
    display: inline-flex;
    flex-direction: column;
    gap: 26px;
    background: #e0f7ff;
    padding: 32px;
    max-width: 331px;
    position: relative;
    border-radius: 10px;
    height: fit-content;
    @media (max-width: 1400px) {
      padding: 15px;
    }
  }

  // .monitoring-one__left-title

  &__left-title {
    color: #007bb3;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    letter-spacing: 0.16px;
    @media (max-width: 1720px) {
      font-size: 14px;
    }
    @media (max-width: 1300px) {
      font-size: 12px;
    }
  }

  // .monitoring-one__left-list

  &__left-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .monitoring-one__left-list-item

  &__left-list-item {
    display: flex;
    gap: 14px;
    @media (max-width: 1300px) {
      gap: 8px;
    }
  }

  &__left-text {
    color: #007bb3;
    font-family: Inter;
    font-size: 14px;
    max-width: 214px;
    span {
      font-weight: 700;
    }
    @media (max-width: 1720px) {
      font-size: 12px;
    }
    @media (max-width: 1400px) {
      max-width: 181px;
    }
    @media (max-width: 1300px) {
      font-size: 9px;
      max-width: 150px;
    }
  }

  // .monitoring-one__center

  &__center {
    z-index: -1;
    margin-top: 116px;
    img {
      height: 708px;
      width: 1113px;
      @media (max-width: 1800px) {
        width: unset;
        height: unset;
      }
    }
  }

  // .monitoring-one__right

  &__right {
  }

  // .monitoring-one__right-items

  &__right-items {
    display: flex;
    flex-direction: column;
    gap: 28px;
  }

  // .monitoring-one__right-item

  &__right-item {
    border-radius: 10px;
    background: #e0f7ff;
    padding: 32px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    max-width: 331px;
    position: relative;
    @media (max-width: 1400px) {
      padding: 15px;
    }
  }

  // .monitoring-one__right-title

  &__right-title {
    color: #007bb3;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.16px;
    @media (max-width: 1720px) {
      font-size: 14px;
    }
    @media (max-width: 1300px) {
      font-size: 12px;
    }
  }

  // .monitoring-one__right-subtitle

  &__right-subtitle {
    color: #007bb3;
    font-family: Inter;
    font-size: 14px;
    @media (max-width: 1720px) {
      font-size: 12px;
    }
    @media (max-width: 1300px) {
      font-size: 9px;
    }
  }

  &__call-phone,
  &__tel,
  &__ear,
  &__sufler,
  &__trilateral,
  &__complete,
  &__overhear {
    fill: rgba(0, 123, 179, 1);
    width: 21.071px;
    height: 21.071px;
  }
  &__right-line1 {
    position: absolute;
    width: 373px;
    height: 1px;
    left: -116%;
    top: 56%;
    z-index: 2;
    border-radius: 10px;
    height: 87px;
    @media (max-width: 1720px) {
      width: 268px;
      top: 49%;
      svg {
        width: 268px;
      }
    }
    @media (max-width: 1400px) {
      width: 232px;
      top: 49%;
      svg {
        width: 232px;
      }
    }
    @media (max-width: 1300px) {
      width: 203px;
      top: 63%;
      svg {
        width: 203px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -19px;
      top: -5px;
      @media (max-width: 1720px) {
        right: -12px;
        top: 6px;
      }
      @media (max-width: 1400px) {
        right: -7px;
        top: 12px;
      }
      @media (max-width: 1300px) {
        right: -7px;
        top: 17px;
        width: 8px;
        height: 8px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: -4px;
      bottom: 0;
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
      }
    }
  }
  &__right-line2 {
    position: absolute;
    left: -13%;
    @media (max-width: 1400px) {
      left: -22%;
    }
    @media (max-width: 1300px) {
      width: 34px;
      left: -14%;
      svg {
        width: 34px;
      }
    }

    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 6px;
      top: 5px;
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
        top: 7px;
        right: 5px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: 2px;
      bottom: 2px;
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
        bottom: 4px;
      }
    }
  }
  &__right-line3 {
    position: absolute;
    left: -35%;
    top: 42%;
    @media (max-width: 1720px) {
      top: 29%;
      svg {
        width: 94px;
      }
    }

    @media (max-width: 1600px) {
      top: 37%;
    }
    @media (max-width: 1400px) {
      left: -42%;
      top: 26%;
    }
    @media (max-width: 1300px) {
      left: -36%;
      top: 29%;
      svg {
        width: 70px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 4px;
      top: 0px;
      @media (max-width: 1720px) {
        right: 3px;
        top: 2px;
      }
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
        right: 2px;
        top: 5px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: 2px;
      bottom: 0px;
      @media (max-width: 1720px) {
        left: 0px;
      }
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
      }
    }
  }
  &__left-line1 {
    position: absolute;
    right: -36%;
    top: 27%;
    @media (max-width: 1720px) {
      right: -28%;
      top: 30%;
      svg {
        width: 100px;
      }
    }
    @media (max-width: 1600px) {
      right: -30%;
      top: 24%;
    }
    @media (max-width: 1400px) {
      right: -36%;
      top: 20%;
    }
    @media (max-width: 1300px) {
      right: -45%;
      top: 23%;
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: 5px;
      top: 0px;
      @media (max-width: 1720px) {
        left: 0px;
        top: 13px;
      }
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
        top: 16px;
        left: 2px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 0px;
      bottom: 0px;
      @media (max-width: 1720px) {
        right: -2px;
      }
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
      }
    }
  }

  // .monitoring-one__footer

  &__footer {
    margin-left: auto;
    margin-right: -40px;
    margin-bottom: 20px;
    border-radius: 10px 0px 0px 10px;
    background: #00aeef;
    box-shadow: 0px 12px 28px 0px rgba(60, 60, 60, 0.11);
    padding: 20px 40px;
    width: 960px;
  }

  // .monitoring-one__title

  &__foort-title {
    color: #fff;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%;
    @media (max-width: 1720px) {
      font-size: 20px;
    }
  }

  // .monitoring-one__subtitle

  &__footer-subtitle {
    color: #fff;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    @media (max-width: 1720px) {
      font-size: 16px;
    }
  }
}