.autocall-work {
  // .autocall-work__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .autocall-work__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 80px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .autocall-work__bottom

  &__bottom {
  }

  // .autocall-work__cards

  &__cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }

  // .autocall-work__step

  &__step {
    width: 100%;
    height: 48px;
    border-radius: 4px 4px 0px 0px;
    background: #0888c2;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.16px;
  }

  // .autocall-work__card

  &__card {
    background-color: #f2fcff;
    min-height: 552px;
    display: flex;
    flex-direction: column;
    min-width: 290px;
    position: relative;
    @media (max-width: 1600px) {
      min-width: unset;
      width: 250px;
      height: 450px;
    }
    @media (max-width: 1400px) {
      width: 220px;
    }
    &:not(:last-child) {
      &::after {
        content: '';
        background: url(../svg/arrow-right.svg);
        position: absolute;
        right: -20%;
        width: 42px;
        height: 28px;
        top: 50%;
        transform: translate(0%, -50%);
        @media (max-width: 1720px) {
          right: -16%;
        }
        @media (max-width: 1600px) {
          right: -19%;
        }
        @media (max-width: 1400px) {
          right: -22%;
        }
        @media (max-width: 1300px) {
          display: none;
        }
      }
    }
  }

  &__content {
    padding: 40px 24px;
    display: flex;
    flex-direction: column;
    gap: 12px;
    @media (max-width: 1400px) {
      padding: 20px;
    }
  }

  // .autocall-work__top

  &__top {
    text-align: center;
    img {
      width: 80px;
      height: 80px;
      @media (max-width: 1400px) {
        width: 50px;
        height: 50px;
      }
    }
  }

  &__center {
    color: #000;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    max-width: 201px;
    margin: 0 auto;
    margin-bottom: 8px;
    @media (max-width: 1600px) {
      font-size: 16px;
    }
  }

  // .autocall-work__center

  &__bottom {
    color: #000;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 140%;
    max-width: 242px;
    @media (max-width: 1600px) {
      font-size: 12px;
    }
  }
}
