.clients {
  // .clients__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .clients__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .clients__bottom

  &__bottom {
    display: flex;
    align-items: center;
    gap: 88px;
    @media (max-width: 1720px) {
      display: block;
      margin: 0 auto;
    }
  }

  // .clients__left

  &__left {
  }

  // .clients__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  // .clients__column

  &__column {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  // .clients__card

  &__card {
    display: flex;
    height: 194px;
    padding: 20px;
    flex-direction: column;
    align-items: flex-start;
    gap: 14px;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
  }

  // .clients__top

  &__top {
    height: 80px;
    img {
      max-width: 280px;
      max-height: 80px;
    }
  }

  // .clients__desc

  &__desc {
    color: #000;
    font-family: Inter;
    font-size: 18px;
    line-height: 140%;
    max-width: 340px;
  }

  // .clients__right

  &__right {
    @media (max-width: 1720px) {
      display: none;
    }
    img {
      width: 506.944px;
      height: 507.064px;
    }
  }
}
