.online {
  // .online__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .online__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 80px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .online__bottom

  &__bottom {
    display: flex;
    align-items: center;
    gap: 150px;
  }

  // .online__left

  &__left {
    img {
      width: 745.631px;
      height: 510px;
      @media (max-width: 1720px) {
        width: unset;
        height: unset;
      }
    }
  }

  // .online__right

  &__right {
    border-radius: 10px;
    border: 1px solid #82d7f7;
    padding: 31px;
  }

  // .online__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 40px;
    position: relative;

    &::after {
      content: '';
      width: 528px;
      height: 424px;
      border-radius: 10px 0px 0px 10px;
      background: #f8f8f8;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }

  // .online__card

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 304px;
    min-height: 304px;
    border-radius: 10px;
    border: 4px solid var(--medium-blue, #95dbfa);
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);

    @media (max-width: 1300px) {
      width: 250px;
    }
  }

  // .online__top

  &__top {
    img {
      width: 70px;
      height: 70px;
      @media (max-width: 1300px) {
        width: 50px;
        height: 50px;
      }
    }
  }

  &__card-bottom {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 188px;
    @media (max-width: 1300px) {
      font-size: 16px;
    }
  }
}
