.constituents {
  &__bg {
    width: 100%;
    position: absolute;
    top: 10%;
    right: 0;
    background: url(../svg/bg-img2.svg);
    height: 377px;
    z-index: -1;
  }

  &__line1 {
    position: absolute;
    border-radius: 10px;
    border: 1px solid rgba(0, 174, 239, 0.18);
    width: 50%;
    height: 66%;
    top: 6%;
    right: -13%;
    z-index: -3;
  }
  &__line2 {
    position: absolute;
    border-radius: 10px;
    border: 1px solid rgba(0, 174, 239, 0.18);
    width: 50%;
    height: 66%;
    top: 29%;
    left: -13%;
    z-index: -2;
  }

  // .constituents__container

  &__container {
  }

  // .constituents__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 46px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .constituents__top

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .constituents__title

  &__title {
  }

  // .constituents__logo

  &__logo {
    img {
      width: 157.781px;
      height: 108px;
    }
  }

  &__lg-container {
    max-width: 1124px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    // z-index: 20;
  }

  // .constituents__cards

  &__cards {
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1010px;
    gap: 20px;
    justify-content: center;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 4px dashed #95dbfa;
      width: 80%;
      height: 93%;
      top: -15%;
      left: 50%;
      transform: translate(-50%);
      z-index: -1;
    }
  }

  // .constituents__cards-title

  &__cards-title {
    font-size: 40px;
    color: #007bb3;
    padding: 0 46px;
    width: fit-content;
    margin: 0 auto;
    background-color: #fff;
    max-width: 210px;
  }

  // .constituents__card

  &__card {
    width: 320px;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    padding: 20px 115px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    img {
      width: 89px;
      height: 140px;
      object-fit: cover;
    }
  }
}
.slide-footer {
  position: relative;
  margin-bottom: 20px;
  &::after {
    content: '';
    background: linear-gradient(-90deg, #03a9f4 0%, rgba(255, 255, 255, 0) 60%);
    width: 100%;
    height: 100%;
    bottom: 0;
    position: absolute;
    left: 0;
  }
  &::before {
    content: '';
    background: linear-gradient(90deg, #03a9f4 0%, rgba(255, 255, 255, 0) 12%);
    width: 100%;
    height: 100%;
    bottom: 0;
    position: absolute;
    left: 0;
    @media (max-width: 1700px) {
      width: 60%;
    }
  }

  &__container {
    max-width: 1920px;
    margin: 0 auto;
    padding: 0 300px;
    width: 100%;
    @media (max-width: 1700px) {
      padding: 0 100px;
    }
  }
  // .slide-footer__list

  &__list {
    display: flex;
    align-items: center;
    gap: 40px;
    position: relative;
    z-index: 20;
  }

  // .slide-footer__list-item

  &__list-item {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  // .slide-footer__link

  &__link {
    color: #353535;
    font-size: 16px;
    font-weight: 500;
  }

  &__globe,
  &__mail,
  &__phone {
    width: 28px;
    height: 28px;
    fill: none;
    stroke: rgba(0, 123, 179, 1);
    stroke-width: 2;
  }
}
