.crm {
  // .crm__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .crm__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding-top: 80px;
  }

  // .crm__bottom

  &__bottom {
  }

  // .crm__cards

  &__cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 10px;
    background: #ddf3fd;
    padding: 40px;
    margin-bottom: 30px;
  }

  // .crm__card

  &__card {
    display: flex;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    padding: 12px 20px;
    min-height: 114px;
  }

  // .crm__left

  &__left {
  }

  // .crm__right

  &__right {
    color: #353535;
    font-family: Inter;
    font-size: 20px;
    font-weight: 500;
    line-height: 150%;
    max-width: 302px;
  }

  // .crm__diagramm

  &__diagramm {
  }
}

.diagramm {
  // .diagramm__browser

  &__browser {
    border-radius: 10px;
    border: 2px solid #03a9f4;
    background: rgba(221, 243, 253, 0.77);
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    display: flex;
    padding: 40px;
    justify-content: center;
    align-items: center;
    max-height: 196px;
    gap: 25px;
  }

  // .diagramm__browser-left

  &__browser-left {
  }

  // .diagramm__browser-right

  &__browser-right {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .diagramm__browser-right-title

  &__browser-right-title {
    color: #007bb3;
    text-align: center;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
  }

  // .diagramm__browser-right-subtitle

  &__browser-right-subtitle {
    color: #353535;
    font-family: Inter;
    font-size: 20px;
    font-weight: 500;
    line-height: 150%;
    max-width: 277px;
  }

  // .diagramm__items

  &__items {
    display: flex;
    justify-content: space-between;
  }

  // .diagramm__item

  &__item {
    display: flex;
    flex-direction: column;
    gap: 4px;
    position: relative;
  }
  &__arrow1 {
    position: absolute;
    bottom: 20px;
    left: 50%;
  }

  // .diagramm__top

  &__top {
    display: flex;
    width: 198px;
    height: 198px;
    padding: 40px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border: 2px solid #03a9f4;
    background: rgba(149, 219, 250, 0.8);
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
  }

  // .diagramm__bottom

  &__bottom {
    display: flex;
    flex-direction: column;
    gap: 12px;
    border-radius: 8px;
    background: #f8f8f8;
    padding: 8px;
    position: absolute;
    left: 14%;
    bottom: 0;
  }

  // .diagramm__title

  &__title {
    color: #007bb3;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-weight: 600;
    line-height: 150%;
    max-width: 157px;
    margin: 0 auto;
  }

  // .diagramm__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: 4px;
    li {
      display: flex;
      align-items: center;
      gap: 12px;
      color: #353535;
      font-family: Inter;
      font-size: 16px;
      font-weight: 500;
      line-height: 150%;
    }
  }

  // .diagramm__phone

  &__phone {
    border-radius: 10px;
    border: 2px solid #03a9f4;
    background: rgba(221, 243, 253, 0.77);
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    display: flex;
    flex-direction: column;
    gap: 29px;
    padding: 40px;
    max-height: 220px;
  }

  // .diagramm__img

  &__img {
  }

  // .diagramm__icons

  &__icons {
  }

  // .diagramm__icon

  &__icon {
    display: flex;
    width: 198px;
    height: 198px;
    padding: 40px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
    border-radius: 10px;
    border: 2px solid #03a9f4;
    background: rgba(149, 219, 250, 0.71);
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
  }
}

.icon-api,
.icon-data,
.icon-excel {
  fill: rgba(3, 169, 244, 1);
  width: 23.273px;
  height: 24px;
}

.crm-tasks {
  // .crm-tasks__wrapper

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 40px;
    padding-bottom: 95px;
    img{
      width: 1259px;
      height: 708px;
    }
  }
}
