.interface {
  // .interface__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .interface__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 73px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .interface__bottom

  &__bottom {
    display: flex;
    align-items: center;
    padding: 0 48px;
    justify-content: space-between;
    @media (max-width: 1300px) {
      padding: unset;
    }
  }

  // .interface__left

  &__left {
  }

  // .interface__img

  &__img {
    img {
      width: 525px;
      height: 519.707px;
      @media (max-width: 1300px) {
       width: 400px;
       height: 400px;
      }
    }
  }

  // .interface__right

  &__right {
  }

  // .interface__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 40px;
    margin: 60px 0;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      width: 688px;
      height: 384px;
      border-radius: 10px;
      border: 8px solid #ddf3fd;
      background: #ddf3fd;
      z-index: -1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      @media (max-width: 1600px) {
        height: 250px;
      }
      @media (max-width: 1300px) {
        width: 400px;
      }
    }
  }

  // .interface__card

  &__card {
    display: flex;
    width: 304px;
    height: 304px;
    padding: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    @media (max-width: 1720px) {
      width: 269px;
      height: 263px;
    }
    @media (max-width: 1600px) {
      width: 220px;
      height: 210px;
    }
    @media (max-width: 1400px) {
      width: 180px;
      height: 180px;
    }
    @media (max-width: 1300px) {
      width: 140px;
      height: 140px;
    }
  }

  // .interface__card-img

  &__card-img {
    img {
      width: 80px;
      height: 80px;
      @media (max-width: 1600px) {
        width: 50px;
        height: 50px;
      }
    }
  }

  // .interface__title

  &__title {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    text-transform: uppercase;
    @media (max-width: 1600px) {
      font-size: 18px;
    }
    @media (max-width: 1300px) {
     font-size: 16px;
    }
  }

  // .interface__subtitle

  &__subtitle {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    text-transform: uppercase;
    @media (max-width: 1600px) {
      font-size: 18px;
    }
    @media (max-width: 1300px) {
      font-size: 16px;
     }
  }
  &__text-top,
  &__text-bottom {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 28px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
  }
}
