.monitoring-second {
  &__left {
    gap: 16px;
    top: 37px;
    @media (max-width: 1600px) {
      top: 26px;
    }
  }

  &__right-item {
    gap: 33px;
  }
  &__right {
    top: -7px;
    position: relative;
    @media (max-width: 1720px) {
      top: -20px;
    }
    @media (max-width: 1600px) {
      top: -50px;
    }
  }

  &__right-line1 {
    top: 26%;
    height: 144px;
    @media (max-width: 1720px) {
      width: 348px;
      svg {
        width: 348px;
      }
      &::before {
        right: -8px;
        top: -3px;
      }
    }
    @media (max-width: 1600px) {
      width: 311px;
      svg {
        width: 311px;
      }
      &::before {
        right: -8px;
        top: 4px;
      }
    }
    @media (max-width: 1400px) {
      width: 284px;
      svg {
        width: 284px;
      }
      &::before {
        right: -11px;
        top: 8px;
      }
    }
    @media (max-width: 1300px) {
      width: 247px;
      top: 24%;
      svg {
        width: 247px;
      }
      &::before {
        right: -13px;
        top: 19px;
      }
      &::after {
        bottom: 14px;
      }
    }
  }

  &__left-line1 {
    position: absolute;
    top: 44%;
    left: 100%;
    @media (max-width: 1400px) {
      width: 325px;
      svg {
        width: 325px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -5px;
      bottom: 0;
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
        right: -4px;
        bottom: 3px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: -8px;
      top: -5px;
      @media (max-width: 1400px) {
        top: 2px;
      }
      @media (max-width: 1300px) {
        width: 8px;
        height: 8px;
        top: 6px;
        left: -4px;
      }
    }
  }

  // .monitoring-second__right-list

  &__right-list {
    display: flex;
    flex-direction: column;
    gap: 21px;

    li {
      display: flex;
      align-items: center;
      gap: 14px;
      color: #007bb3;
      font-family: Inter;
      font-size: 14px;
      font-weight: 500;
      svg {
        width: 17.659px;
        height: 18px;
      }
    }
  }

  &__right-text {
    display: flex;
    flex-direction: column;
    gap: 10px;
    p {
      color: #007bb3;
      font-family: Inter;
      font-size: 14px;
      font-weight: 400;
    }
  }
}
.icon-phone-panel {
  fill: #0af121;
}
.icon-second-phone-panel {
  fill: #fdb935;
}
.icon-pause-phone-panel {
  fill: #0028f8;
}

.icon-off-phone-panel {
  fill: #e31010;
}
