.call-report-one {
  &__center {
    @media (max-width: 1300px) {
      margin-top: 211px;
    }
  }
  &__left {
    position: relative;
    bottom: 31px;
    @media (max-width: 1400px) {
      padding: 25px;
      max-width: 270px;
    }
  }
  &__right-items {
    gap: 12px;
  }
  &__right {
    position: relative;
    bottom: 55px;
    @media (max-width: 1600px) {
      bottom: 109px;
    }
    @media (max-width: 1300px) {
      bottom: 85px;
    }
  }
  &__left-text-nodots {
    color: #007bb3;
    font-family: Inter;
    font-size: 14px;
    font-weight: 400;
    position: relative;
    svg {
      border-radius: 2.636px;
      background: #eaeaea;
    }
  }
  &__left-text {
    color: #007bb3;
    font-family: Inter;
    font-size: 14px;
    font-weight: 400;
    position: relative;
    @media (max-width: 1400px) {
      font-size: 12px;
    }
    &::before {
      content: '•';
      position: absolute;
      left: -15px;
    }

    span {
      font-weight: 700;
    }
  }
  &__right-item {
    @media (max-width: 1400px) {
      padding: 25px;
      max-width: 270px;
    }
  }
  &__left-line1 {
    position: absolute;
    right: -27%;
    top: 46.6%;
    @media (max-width: 1720px) {
      right: -22%;
      top: 43%;
      svg {
        width: 80px;
      }
    }
    @media (max-width: 1600px) {
      top: 39.8%;
    }
    @media (max-width: 1400px) {
      top: 41.5%;
      right: -27%;
    }
    @media (max-width: 1300px) {
      top: 50.9%;
      right: -23%;

      svg {
        width: 68px;
      }
    }

    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: 0px;
      top: 5px;
    }
  }
  &__left-line2 {
    position: absolute;
    right: 98%;
    top: 94%;
    @media (max-width: 1720px) {
      top: 89%;
      svg {
        width: 890px;
      }
    }
    @media (max-width: 1600px) {
      top: 93%;
      &::after {
        top: -1px !important;
      }
      svg {
        width: 767px;
      }
    }
    @media (max-width: 1400px) {
      top: 95%;
      svg {
        width: 744px;
      }
    }
    @media (max-width: 1300px) {
      top: 95%;
      svg {
        width: 622px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: -5px;
      bottom: 0;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 0;
      top: -5px;
    }
  }
  &__left-line3 {
    position: absolute;
    position: absolute;
    right: 92%;
    top: 1.2%;

    @media (max-width: 1720px) {
      right: 87%;
      svg {
        width: 800px;
      }
    }
    @media (max-width: 1600px) {
      top: -0.4%;
      svg {
        width: 716px;
      }
    }
    @media (max-width: 1400px) {
      top: 2.6%;
      svg {
        width: 679px;
      }
    }
    @media (max-width: 1300px) {
      top: 1.6%;

      svg {
        width: 576px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: -5px;
      bottom: 0;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 0;
      top: -5px;
    }
  }
  &__left-line4 {
    position: absolute;
    right: 103%;
    top: -114%;
    @media (max-width: 2050px) {
      top: -118%;
      svg {
        &:nth-child(1) {
          width: 715px;
        }
      }
    }
    @media (max-width: 1920px) {
      top: -117%;
      svg {
        &:nth-child(1) {
          width: 717px;
        }
      }
    }
    @media (max-width: 1720px) {
      top: -137%;

      svg {
        &:nth-child(1) {
          width: 619px;
        }
      }
    }
    @media (max-width: 1620px) {
      top: -163%;
      svg {
        &:nth-child(1) {
          width: 542px;
        }
        &:nth-child(2) {
          bottom: 4px;
        }
      }
    }
    @media (max-width: 1400px) {
      top: -159%;
    }
    @media (max-width: 1300px) {
      top: -165%;
      svg {
        &:nth-child(1) {
          width: 442px;
        }
        &:nth-child(2) {
          bottom: 22px;
        }
      }
    }

    &--line {
      position: absolute;
      bottom: -25px;
      right: -20px;
      z-index: -1;
      @media (max-width: 1720px) {
        bottom: -9px;
        right: -21px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -19px;
      bottom: -30px;
      @media (max-width: 1720px) {
        bottom: -16px;
      }
      @media (max-width: 1620px) {
        bottom: -2px;
      }
      @media (max-width: 1300px) {
        right: -14px;
        bottom: 17px;
      }
    }
  }
  &__left-line5 {
    position: absolute;
    left: -12%;
    top: -141%;

    @media (max-width: 1720px) {
      left: -13%;
      top: -152%;
    }
    @media (max-width: 1600px) {
      left: -16%;
      top: -170%;
    }
    @media (max-width: 1400px) {
      left: -20%;
      top: -123%;
    }
    @media (max-width: 1300px) {
      left: -20%;
      top: -142%;
    }

    svg {
      &:nth-child(2) {
        position: absolute;
        bottom: -31px;
        right: -21px;
      }
    }

    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -26px;
      bottom: -34px;
    }
  }
}

.call-report-two {
  padding-top: 150px;

  &__footer {
    margin-left: -40px;
    border-radius: 0px 10px 10px 0px;
  }
  &__center {
    @media (max-width: 1300px) {
      margin-top: 152px;
    }
  }
  &__left {
    bottom: -85px;
  }
  &__left-line2 {
    @media (max-width: 1720px) {
      top: 84%;
    }
    @media (max-width: 1600px) {
      top: 94%;
    }
  }
  &__left-line3 {
    @media (max-width: 1720px) {
      right: 97%;
      top: 18.6%;
    }
    @media (max-width: 1600px) {
      right: 97%;
      top: 0.6%;
    }
    @media (max-width: 1400px) {
      top: 5.6%;
    }
  }
  &__left-line5 {
    @media (max-width: 2050px) {
      top: -145%;
    }
    @media (max-width: 1920px) {
      top: -144%;
    }
    @media (max-width: 1720px) {
      top: -154%;
    }
    @media (max-width: 1600px) {
      top: -167%;
    }
    @media (max-width: 1400px) {
      top: -120%;
    }
    @media (max-width: 1300px) {
      top: -149%;
    }
  }
  &__left-line4 {
    @media (max-width: 2050px) {
      top: -123%;
    }
    @media (max-width: 1720px) {
      top: -143%;
    }
    @media (max-width: 1600px) {
      top: -161%;
    }
    @media (max-width: 1400px) {
      top: -156%;
    }
    @media (max-width: 1300px) {
      top: -172%;
      svg {
        &:nth-child(1) {
          width: 460px;
        }
      }
      &::before {
        right: -24px;
        bottom: 17px;
      }
    }
  }
  &__left-line1 {
    top: 44.5%;
    @media (max-width: 1720px) {
      right: -22%;
      top: 59.5%;
    }
    @media (max-width: 1600px) {
      top: 55.5%;
    }
    @media (max-width: 1400px) {
      top: 60.5%;
      svg {
        width: 67px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 0;
      top: 4px;
    }
  }
  &__right {
    position: relative;
    bottom: 49px;
    @media (max-width: 1600px) {
      bottom: 114px;
    }
  }
}

.call-report-third {
  &__left {
    top: 2.4%;
  }
  &__left-line4 {
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: 0;
      top: 0px;
      @media (max-width: 1720px) {
        top: 17px;
      }
      @media (max-width: 1600px) {
        top: 28px;
      }
      @media (max-width: 1300px) {
        top: 47px;
      }
    }
  }
  &__left-line1 {
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 0;
      top: 5px;
    }
  }
  &__left-line5 {
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      top: -4px;
      left: -5px;
      @media (max-width: 1720px) {
        left: -2px;
      }
      @media (max-width: 1600px) {
        left: -5px;
      }
      @media (max-width: 1400px) {
        left: 0px;
      }
    }
  }
}

.call-report-fourth {
  &__right {
    position: absolute;
    right: 0;
    top: 44%;
    transform: translate(0, -50%);
  }
  &__right-line1 {
    position: absolute;
    right: 59%;
    top: -20%;

    @media (max-width: 1920px) {
      right: 63%;
    }
    @media (max-width: 1720px) {
      right: 60%;
      svg {
        width: 278px;
      }
    }
    @media (max-width: 1600px) {
      right: 57%;
      svg {
        width: 243px;
      }
    }
    @media (max-width: 1400px) {
      display: none;
    }

    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -6px;
      bottom: -20px;
      @media (max-width: 1720px) {
        bottom: -13px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: -4px;
      top: -4px;
      @media (max-width: 1720px) {
        top: 0px;
      }
      @media (max-width: 1600px) {
        top: 2px;
      }
    }
  }
  &__right-line2 {
    position: absolute;
    bottom: -30%;
    left: -258%;
    @media (max-width: 1920px) {
      left: -262%;
    }
    @media (max-width: 1720px) {
      bottom: -33%;
      left: -215%;
      svg {
        width: 847px;
      }
    }
    @media (max-width: 1600px) {
      left: -184%;

      svg {
        width: 781px;
      }
    }
    @media (max-width: 1400px) {
      display: none;
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -5px;
      top: 0;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      bottom: -4px;
      left: 0px;
      @media (max-width: 1720px) {
        bottom: 1px;
        left: -6px;
      }
      @media (max-width: 1600px) {
        bottom: 5px;
      }
    }
  }
}

.icon-print,
.icon-file {
  width: 15.814px;
  height: 15.814px;
  fill: rgba(234, 234, 234, 1);
  stroke: rgba(129, 130, 133, 1);
}
