.operator {
  // .operator__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .operator__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 33px;
    padding-top: 80px;
    padding-bottom: 100px;
  }

  // .operator__bottom

  &__bottom {
    min-height: 635px;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      z-index: -1;
      border-radius: 10px;
      border: 8px solid #ddf3fd;
      background: #ddf3fd;
      width: 270px;
      height: 384px;
    }
    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(0, -50%);
      z-index: -1;
      border-radius: 10px;
      border: 8px solid #ddf3fd;
      background: #ddf3fd;
      width: 270px;
      height: 384px;
    }
  }

  // .operator__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 40px;
  }

  // .operator__card

  &__card {
    display: flex;
    flex-direction: column;
    gap: 25px;
    width: 304px;
    min-height: 240px;
    padding: 20px;
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    align-items: center;
    justify-content: center;
    @media (max-width: 1720px) {
      width: 250px;
    }
    @media (max-width: 1400px) {
      width: 200px;
      height: 200px;
    }
    &:nth-child(1) {
      position: absolute;
      bottom: 0;
      left: 8%;
    }
    &:nth-child(2) {
      position: absolute;
      bottom: 42%;
      left: 13%;
    }
    &:nth-child(3) {
      position: absolute;
      top: 0;
      left: 32%;
      @media (max-width: 1400px) {
        top: 30px;
      }
    }
    &:nth-child(4) {
      position: absolute;
      top: 0;
      right: 32%;
      @media (max-width: 1400px) {
        top: 30px;
      }
    }
    &:nth-child(5) {
      position: absolute;
      bottom: 42%;
      right: 13%;
    }
    &:nth-child(6) {
      position: absolute;
      bottom: 0;
      right: 8%;
    }
  }
  &__center-img {
  }
  &__bottom-img {
    position: absolute;
    bottom: -35%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    border: 1px solid #e5e5e5;
    padding: 28px 80px;
    z-index: 10;
    background-color: #fff;
    @media (max-width: 1300px) {
      bottom: -14%;
      img {
        width: 200px;
      }
    }
  }
  &__bottom-line {
    border-radius: 10px;
    border: 1px solid #82d7f7;
    width: 1300px;
    height: 159px;
    bottom: 0;
    position: absolute;
    left: 20%;
  }
  &__bottom-line2 {
    border-radius: 10px;
    border-radius: 10px;
    border-top: 4px dashed #95dbfa;
    border-right: 4px dashed #95dbfa;
    border-left: 4px dashed #95dbfa;
    width: 1019px;
    height: 421px;
    top: 30%;
    left: 22%;
    position: absolute;
  }

  // .operator__card-img

  &__card-img {
    img {
      width: 70px;
      height: 70px;
      @media (max-width: 1400px) {
        width: 40px;
        height: 40px;
      }
    }
  }

  // .operator__card-title

  &__card-title {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 188px;
    @media (max-width: 1720px) {
      font-size: 16px;
    }
    @media (max-width: 1400px) {
      font-size: 13px;
    }
  }
}

.operator-place-one {
  // .operator-place-one__left

  &__left {
  }

  // .operator-place-one__left-items

  &__left-items {
    display: flex;
    flex-direction: column;
    gap: 12px;
    position: relative;
    @media (max-width: 1600px) {
      bottom: 51px;
    }
  }
  &__left-list {
    @media (max-width: 1400px) {
      svg {
        width: 15px;
      }
    }
  }

  // .operator-place-one__left-item

  &__left-item {
    background-color: #e0f7ff;
    width: 332px;
    padding: 32px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    position: relative;
    border-radius: 10px;
    @media (max-width: 1400px) {
      width: 220px;
      &:last-child {
        display: none;
      }
    }
  }

  // .operator-place-one__right

  &__right {
    position: relative;
  }

  // .operator-place-one__block

  &__block {
    background-color: #e0f7ff;
    width: 332px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 30px;
    border-radius: 10px;
    position: relative;
    @media (max-width: 1400px) {
      width: 220px;
    }
  }

  // .operator-place-one__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .operator-place-one__list-item

  &__list-item {
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
  }

  // .operator-place-one__btn

  &__btn {
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px; /* 100% */
    font-variant: all-small-caps;
    width: 100px;
    height: 30px;
    border-radius: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    &--green {
      background: #42a64c;
    }
    &--gray {
      background: #818285;
    }
    &--orange {
      background: #e2a812;
    }
    &--red {
      background: #d43d3d;
    }
    &--blue {
      background: #3c66b9;
    }
  }

  // .operator-place-one__btn-desc

  &__btn-desc {
    color: #007bb3;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.14px;
  }

  // .operator-place-one__wrapper

  &__wrapper {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__line1 {
    position: absolute;
    left: 97%;
    z-index: 20;
    top: 74.4%;
    transform: translate(0%, -50%);
    @media (max-width: 1720px) {
      top: 71.4%;
      svg {
        width: 83px;
      }
    }
    @media (max-width: 1600px) {
      top: 87.4%;
      svg {
        width: 70px;
      }
    }
    @media (max-width: 1400px) {
      top: 75.4%;
    }
    @media (max-width: 1300px) {
      top: 72%;
      svg {
        width: 63px;
      }
    }
  }

  &__line2 {
    position: absolute;
    left: 97%;
    top: 41.6%;
    transform: translate(0%, -50%);
    width: 266px;
    z-index: 20;
    @media (max-width: 1720px) {
      width: 220px;
      svg {
        width: 210px;
      }
    }
    @media (max-width: 1600px) {
      width: 190px;
      svg {
        width: 175px;
      }
    }
    @media (max-width: 1300px) {
      width: 160px;
      top: 12.6%;
      svg {
        width: 153px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: 0;
      bottom: 0;
      @media (max-width: 1720px) {
        bottom: 1px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      bottom: 0;
      left: 0;
      @media (max-width: 1720px) {
        bottom: 1px;
      }
    }
  }

  &__line3 {
    position: absolute;
    right: 100%;
    top: 24%;
    transform: translate(0%, -50%);
    z-index: 20;
    @media (max-width: 1600px) {
      top: 21%;
      svg {
        width: 200px;
      }
    }
    @media (max-width: 1400px) {
      top: 25%;
    }
    @media (max-width: 1300px) {
      top: 23%;
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -7px;
      bottom: 10px;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      bottom: 0;
      left: -3px;
    }
  }
  &__line4 {
    position: absolute;
    left: 100%;
    bottom: 45%;
    z-index: 20;
    @media (max-width: 1720px) {
      bottom: 67%;
    }
    @media (max-width: 1600px) {
      width: 132px;
      bottom: 87%;
      svg {
        width: 132px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      top: 0;
      right: -4px;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      bottom: -4px;
      left: -3px;
      @media (max-width: 1600px) {
        bottom: 6px;
        left: -8px;
      }
    }
  }
}

.operator-place-two {
  &__left {
    top: 199px;
    @media (max-width: 1300px) {
      top: 187px;
    }
  }
  &__line2 {
  }
  &__line1 {
    position: absolute;
    left: 98%;
    top: 23%;
    @media (max-width: 1720px) {
      top: 21%;
      svg {
        width: 239px;
      }
    }
    @media (max-width: 1600px) {
      top: 11%;

      svg {
        width: 212px;
      }
    }
    @media (max-width: 1400px) {
      svg {
        width: 197px;
      }
    }
    @media (max-width: 1300px) {
      svg {
        width: 178px;
      }
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      top: 4px;
      right: -4px;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      bottom: 3px;
      left: -3px;
    }
  }
  &__center {
    padding-bottom: 88px;
  }
  &__right {
    @media (max-width: 1720px) {
      top: 68px;
    }
    @media (max-width: 1300px) {
      top: 91px;
    }
  }
}
