.possibilities {
  // .possibilities__container

  &__container {
  }

  // .possibilities__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 157px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .possibilities__bottom

  &__bottom {
  }

  // .possibilities__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 12px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      bottom: 5%;
      left: 30%;
      background: url(../svg/bg-img.svg);
      width: 700px;
      height: 454px;
      z-index: -1;
      border-radius: 10px;
    }
  }

  // .possibilities__card

  &__card {
    border-radius: 10px;
    background: #ddf3fd;
    padding: 20px 20px 24px 20px;
    display: inline-flex;
    flex-direction: column;
    gap: 24px;
    min-height: 451px;
    &:nth-child(even) {
      position: relative;
      bottom: 60px;
    }
    @media (max-width: 1700px) {
      width: 100%;
    }
  }

  // .possibilities__card-title

  &__card-title {
    color: #007bb3;
    font-size: 24px;
    font-weight: 600;
    line-height: 140%;
    text-align: center;
  }

  &__card-column {
    display: flex;
    flex-direction: column;
    gap: 12px;
    @media (max-width: 1700px) {
      width: 100%;
    }
  }

  // .possibilities__card-wrapper

  &__card-wrapper {
    display: flex;
    align-items: center;
    gap: 12px;
    @media (max-width: 1600px) {
      flex-direction: column;
    }
  }

  // .possibilities__card-item

  &__card-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 13px 12px;
    background-color: #fff;
    @media (max-width: 1600px) {
    }
  }

  // .possibilities__card-item-left

  &__card-item-left {
    img {
      width: 52px;
      height: 52px;
    }
  }

  // .possibilities__card-item-right

  &__card-item-right {
  }

  // .possibilities__card-item-title

  &__card-item-title {
    color: #353535;
    font-size: 16px;
    font-weight: 400;
    line-height: 120%;
    max-width: 104px;
    word-wrap: break-word;
    @media (max-width: 1720px) {
      word-wrap: unset;
    }
  }

  // .possibilities__line1

  &__line1 {
    position: absolute;
    border-radius: 10px;
    border: 1px solid rgba(0, 174, 239, 0.49);
    width: 441px;
    height: 347px;
    left: 13%;
    bottom: -10%;
    z-index: -2;
    @media (max-width: 1700px) {
      width: 295px;
      bottom: 2%;
    }
  }

  // .possibilities__line2

  &__line2 {
    position: absolute;
    border-radius: 10px;
    border: 1px solid rgba(0, 174, 239, 0.49);
    width: 441px;
    height: 347px;
    right: 12%;
    top: -22%;
    z-index: -2;
    @media (max-width: 1700px) {
      right: 12%;
      top: -6%;
      width: 299px;
      height: 347px;
    }
  }
}
