.constructor {
  // .constructor__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .constructor__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 138px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .constructor__bottom

  &__bottom {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 1px solid rgba(0, 174, 239, 0.49);
      width: 847px;
      height: 330px;
      right: 22.4%;
      top: -7%;
      z-index: -2;
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 1px solid rgba(203, 203, 203, 0.49);
      width: 868px;
      height: 330px;
      left: 22.4%;
      bottom: -7%;
      z-index: -2;
    }
  }

  // .constructor__cards

  &__cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 90px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      bottom: -12%;
      left: 13%;
      width: 690px;
      height: 264px;
      border-radius: 10px;
      background: #ddf3fd;
      z-index: -1;
    }
    &::before {
      content: '';
      position: absolute;
      top: -12%;
      right: 14%;
      width: 690px;
      height: 264px;
      border-radius: 10px;
      background: #f8f8f8;
      z-index: -1;
    }
    @media (max-width: 1300px) {
      gap: 15px;
    }
  }

  // .constructor__card

  &__card {
    border-radius: 10px;
    border: 4px solid #95dbfa;
    padding: 20px;
    background: #fff;
    width: 304px;
    min-height: 366px;
    display: flex;
    align-items: center;
    gap: 20px;
    flex-direction: column;
    @media (max-width: 1720px) {
      width: 250px;
    }
    @media (max-width: 1400px) {
      width: 200px;
      height: 250px;
      gap: 10px;
    }
  }

  // .constructor__card-img

  &__card-img {
    img {
      width: 80px;
      height: 80px;
      @media (max-width: 1400px) {
        width: 40px;
        height: 40px;
      }
    }
  }

  // .constructor__card-title

  &__card-title {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-weight: 500;
    line-height: 150%;
    @media (max-width: 1720px) {
      font-size: 16px;
    }
    @media (max-width: 1400px) {
      font-size: 13px;
    }
  }

  // .constructor__card-subtitle

  &__card-subtitle {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 150%;
    @media (max-width: 1720px) {
      font-size: 12px;
    }
    @media (max-width: 1400px) {
      font-size: 9px;
    }
  }
}
