.how-it-works {
  padding-block: 65px 75px;
  // @include media-breakpoint-down(xl) {
  //   display: none;
  // }

  @include media-breakpoint-down(lg) {
    &.hide-mobile {
      .how-it-works__table {
        position: relative;
        max-height: 800px;
        &::before {
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          z-index: 15;
          box-shadow: inset 0 -200px 200px #fff;
        }
      }
    }
  }

  .line-h {
    height: 2px;
    background-color: #e9e9e9;
    transition: width 750ms linear;
    transition-delay: 750ms;
    z-index: -1;

    &.global {
      position: absolute;
      width: 650px;
      top: 417px;
      left: 40%;
      transform: translateX(-50%);
      transition-delay: 500ms;
    }
  }

  .line-v {
    width: 2px;
    background-color: #e9e9e9;
    transition: height 750ms linear;
    transition-delay: 750ms;
    z-index: -1;

    &.global {
      position: absolute;
      height: 300px;
      top: 51%;
      left: 40.4%;
      transform: translateY(-50%);
      transition-delay: 500ms;
    }
  }

  .how-it-works__table-box {
    position: relative;

    &.top,
    &.bottom {
      .line-h {
        width: calc(100% - 86px);
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
      }

      .line-v {
        position: absolute;

        &.right,
        &.left {
          height: 50px;
        }

        &.right {
          right: 42.5px;
        }

        &.left {
          left: 42.5px;
        }

        &.long {
          height: 77px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }

    &.top {
      .line-h {
        top: calc(100% + 50px);
      }

      .line-v {
        &.right,
        &.left {
          top: 100%;
        }

        &.long {
          top: calc(100% + 50px);
        }
      }
    }

    &.bottom {
      .line-h {
        bottom: calc(100% + 50px);
      }

      .line-v {
        &.right,
        &.left {
          bottom: 100%;
        }

        &.long {
          bottom: calc(100% + 50px);
        }
      }
    }

    &.top-right {
      .line-v,
      .line-h {
        position: absolute;
      }

      .line-h {
        width: 50px;
        top: 50%;
        left: 100%;
        transform: translateY(-50%);
      }

      .line-v {
        height: 279px;
        top: calc(50% - 1px);
        left: calc(100% + 50px);
        transform-origin: top left;

        .line-h {
          width: 235px;
          right: 100%;
          bottom: -4px;
          left: initial;
          top: initial;
          transform: none;
        }
      }
    }

    .how-it-works__table-list {
      position: relative;

      .line-v {
        height: calc(100% - 101px);

        position: absolute;
        top: calc(50% + 1px);
        transform: translateY(-50%);

        &.right {
          left: calc(100% + 48px);
        }

        &.left {
          right: calc(100% + 48px);
        }
      }
    }

    .how-it-works__table-item {
      position: relative;

      .line-h {
        width: 50px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);

        &.right {
          left: 100%;
        }

        &.left {
          right: 100%;
        }

        &.long {
          width: 300px;

          &.left {
            right: calc(100% + 48px);
          }

          &.right {
            width: 235px;
            left: calc(100% + 46px);
          }
        }
      }
    }

    &.communication {
      .line-v {
        top: calc(50% + 2px);
        height: calc(100% - 103px);
      }
    }
  }

  &.type-2 {
    .line-h {
      &.global {
        top: 535px;
      }
    }

    .line-v {
      &.global {
        height: 456px;
        top: 44.7%;
        left: 44.4%;
      }
    }

    .how-it-works__table-box {
      &.top-right {
        .line-v {
          height: 400px;
        }
      }

      .how-it-works__table-list {
        position: relative;

        .line-v {
          height: calc(100% - 89px);
          position: absolute;
          top: calc(50% + 1px);
          transform: translateY(-50%);

          &.right {
            left: calc(100% + 48px);
          }

          &.left {
            right: calc(100% + 48px);
          }
        }
      }

      .how-it-works__table-item {
        position: relative;

        .line-h {
          width: 50px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);

          &.right {
            left: 100%;
          }

          &.left {
            right: 100%;
          }

          &.long {
            width: 300px;

            &.left {
              right: calc(100% + 48px);
            }

            &.right {
              width: 235px;
              left: calc(100% + 48px);
            }
          }
        }
      }

      &.communication {
        .line-v {
          top: calc(50% + 2px);
          height: calc(100% - 86px);
        }
      }
    }
  }

  .section-title {
    margin-bottom: 70px;
    text-align: center;
  }

  &__text {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: $black;
  }

  &__wrap {
    margin: -15px;
    padding: 15px;
    overflow: auto;
    // @include customScrollbar(2, transparent, rgba(3, 169, 244, 1), 5);

    &:not(.aos-animate) {
      .line-h {
        width: 0 !important;
      }

      .line-v {
        height: 0 !important;
      }
    }
  }

  &__btn {
    margin: 0 auto;
    @include media-breakpoint-up(lg) {
      display: none;
    }
  }

  &__table {
    position: relative;
    width: 100%;
    min-height: 725px;
    fill: rgba(3, 169, 244, 1);
    min-width: 1400px;
    overflow: hidden;
    @include media-breakpoint-down(lg) {
      min-width: 100%;
      min-height: fit-content;
      display: flex;
      flex-direction: column;
      align-items: center;

      .how-it-works__table-img,
      .how-it-works__table-box.office,
      .how-it-works__table-box.communication,
      .how-it-works__table-box.top,
      .how-it-works__table-box.left,
      .how-it-works__table-box.bottom,
      .how-it-works__table-box.top-right {
        padding-inline: 0;
        position: relative !important;
        left: 0 !important;
        right: 0 !important;
        bottom: 0 !important;
        left: 0 !important;
        max-width: fit-content;

        .list-group-primary {
          padding-left: 40px;
          text-align: start;
        }

        .how-it-works__table-item {
          margin-bottom: 30px;

          &::after {
            content: '';
            display: block;
            width: 2px;
            height: 60px;
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            background-color: #e9e9e9;
          }
        }
      }

      .how-it-works__table-img {
        max-width: 270px;
        min-height: 184px;
        margin-bottom: 75px;
        order: 1;
        z-index: 15;
      }

      .how-it-works__table-box {
        margin-bottom: 45px;
      }

      .how-it-works__table-box.top,
      .how-it-works__table-box.bottom {
        max-width: 270px;
        width: 100%;
        text-align: center;

        &::after {
          content: '';
          display: block;
          width: 2px;
          height: 30px;
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          background-color: #e9e9e9;
        }
      }

      .how-it-works__table-box.office {
        margin-bottom: 10px;
        order: 2;
        .how-it-works__table-item {
          &:nth-child(2),
          &:nth-child(3) {
            display: none;
          }
        }

        &::after {
          content: '';
          display: block;
          width: 2px;
          height: 70px;
          position: absolute;
          bottom: calc(100% + 15px);
          left: 50%;
          transform: translateX(-50%);
          background-color: #e9e9e9;
        }
      }

      .how-it-works__table-box.communication {
        order: 5;
        margin-bottom: 20px;
        .how-it-works__table-item {
          &:nth-child(2),
          &:nth-child(3) {
            display: none;
          }
        }
      }

      .how-it-works__table-box.top {
        order: 3;
        &::after {
          height: 60px;
        }
      }

      .how-it-works__table-box.left {
        order: 4;
        z-index: 10;
      }

      .how-it-works__table-box.bottom {
        order: 4;
      }

      .how-it-works__table-box.top-right {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 140px;
        width: 100%;
        order: 6;
      }

      .line-h,
      .line-v {
        display: none;
      }
    }

    .icon {
      &-robot {
        width: 44px;
        min-width: 44px;
        height: 44px;
        fill: none;
        stroke: rgba(3, 169, 244, 1);
      }
      &-robot-dialog {
        width: 52px;
        min-width: 52px;
        height: 52px;
        fill: none;
        stroke: rgba(3, 169, 244, 1);
      }
      &-how-it-works-icon-1 {
        width: 49px;
        min-width: 49px;
        height: 49px;
      }

      &-how-it-works-icon-2 {
        width: 54px;
        min-width: 54px;
        height: 44px;
      }

      &-how-it-works-icon-3 {
        width: 46px;
        min-width: 46px;
        height: 57px;
      }

      &-how-it-works-icon-4 {
        width: 45px;
        min-width: 45px;
        height: 45px;
      }

      &-how-it-works-icon-5 {
        width: 48px;
        min-width: 48px;
        height: 48px;
      }

      &-how-it-works-icon-6 {
        width: 46px;
        min-width: 46px;
        height: 46px;
      }

      &-how-it-works-icon-7 {
        width: 52px;
        min-width: 52px;
        height: 48px;
      }

      &-how-it-works-icon-8 {
        width: 62px;
        min-width: 62px;
        height: 48px;
      }

      &-how-it-works-icon-9 {
        width: 46px;
        min-width: 46px;
        height: 46px;
      }

      &-how-it-works-icon-10 {
        width: 43px;
        min-width: 43px;
        height: 43px;
      }

      &-how-it-works-icon-11 {
        width: 34px;
        min-width: 34px;
        height: 40px;
      }

      &-how-it-works-icon-12 {
        width: 45px;
        min-width: 45px;
        height: 45px;
      }

      &-how-it-works-icon-13 {
        width: 58px;
        min-width: 58px;
        height: 58px;
      }

      &-how-it-works-icon-14 {
        width: 51px;
        min-width: 51px;
        height: 51px;
      }

      &-how-it-works-icon-15 {
        width: 54px;
        min-width: 54px;
        height: 54px;
      }
    }

    &-img {
      display: block;
      max-width: 388px;
      width: 100%;
      position: absolute;
      top: 32.7%;
      left: 26.5%;
      z-index: 100;
      @include media-down(1270) {
        max-width: 290px;
        top: 35.7%;
        left: 29.5%;
      }

      #circle {
        .circle {
          transition: opacity 250ms ease;

          &.hide {
            opacity: 0;
          }
        }
      }
    }

    &-box {
      width: 100%;
      position: relative;

      .block-title {
        margin-bottom: 5px;
        text-align: center;
      }

      > .how-it-works__text {
        margin-bottom: 25px;
        text-align: center;
      }

      &.office {
        max-width: 322px;
        padding-right: 50px;
        position: absolute;
        top: 7.3%;
        left: 0;
      }

      &.communication {
        max-width: 372px;
        padding-inline: 50px;
        position: absolute;
        top: 26.95%;
        left: 57.9%;
        @media (max-width: 1300px) {
          left: 53.9%;
        }

        .block-title {
          margin-bottom: 25px;
        }
      }

      &.top {
        max-width: 272px;
        padding: 22px 40px;
        background: $white;
        border: 2px solid #e9e9e9;
        border-radius: 5px;
        position: absolute;
        top: 0;
        left: 31%;

        .how-it-works__table-box-icons {
          margin-bottom: 16px;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 53px;
        }
      }

      &.bottom {
        max-width: 276px;
        padding: 17px 12px 22px 15px;
        background: $white;
        border: 2px solid #e9e9e9;
        border-radius: 5px;
        position: absolute;
        bottom: 0;
        left: 30.5%;

        .how-it-works__text {
          text-align: center;
        }

        .how-it-works__table-box-icons {
          margin-bottom: 16px;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 53px;
        }

        .list-group-primary {
          &__item {
            margin: 0;
            font-weight: 300;
            font-size: 16px;
            line-height: 150%;
          }
        }
      }

      &.top-right {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        max-width: 138px;
        min-height: 139px;
        padding: 26px 44px 22px 37px;
        background: $white;
        border: 2px solid #e9e9e9;
        border-radius: 5px;
        position: absolute;
        top: 9%;
        right: 50px;
        @media (max-width: 1400px) {
          top: 8.8%;
          right: 114px;
        }
        @media (max-width: 1300px) {
          display: none;
        }

        .how-it-works__table-box-icons {
          margin-bottom: 13px;
        }
      }

      &.left {
        max-width: 272px;
        padding: 34px 42px 30px 42px;
        background: $white;
        border: 2px solid #e9e9e9;
        border-radius: 5px;
        position: absolute;
        left: 0;
        top: 41%;

        .how-it-works__table-box-icons {
          margin-bottom: 20px;

          .icon {
            display: block;
            margin: 0 auto;
          }
        }
      }
    }

    &-list {
      width: 100%;
      max-width: 272px;
      position: relative;
    }

    &-item {
      width: 100%;
      display: flex;
      align-items: center;
      padding: 20px 35px 20px 33px;
      background: $white;
      border: 2px solid #e9e9e9;
      border-radius: 5px;
      gap: 15px;
      position: relative;

      &:not(:last-of-type) {
        margin-bottom: 20px;
      }
    }
  }
}
.how-it-works.type-2 {
  padding: 8px 0 16px;

  .how-it-works__wrap {
    margin: 0;
    padding-block: 56px 40px;
    border: 1px solid #e9e9e9;
    border-radius: 5px;
    @include media-breakpoint-down(lg) {
      border: 0;
    }

    .how-it-works__table {
      margin: 0 auto;
      max-width: 1440px;
      min-height: 976px;

      &:after {
        top: 14%;
        height: 400px;
      }

      .how-it-works__table-img {
        top: 35.7%;
        left: 28.5%;
        @include media-down(1270) {
          top: 41.7%;
          left: 33.5%;
        }
      }

      .how-it-works__table-box.top {
        left: 35%;

        &:before {
          @include media-down(1270) {
            height: 265px;
          }
        }
      }

      .how-it-works__table-box.communication {
        top: 34.2%;
        left: 58.9%;
      }

      .how-it-works__table-box.bottom {
        max-width: 412px;
        left: 30%;
      }

      .how-it-works__table-box.left {
        top: 44%;
      }
    }
  }

  .how-it-works__table {
    @include media-breakpoint-down(lg) {
      .how-it-works__table-img,
      .how-it-works__table-box.office,
      .how-it-works__table-box.communication,
      .how-it-works__table-box.top,
      .how-it-works__table-box.left,
      .how-it-works__table-box.bottom,
      .how-it-works__table-box.top-right {
        padding-inline: 0;
        position: relative !important;
        left: 0 !important;
        right: 0 !important;
        bottom: 0 !important;
        left: 0 !important;
        z-index: 10;
        max-width: fit-content;

        .list-group-primary {
          padding-left: 40px;
          text-align: start;
        }

        .how-it-works__table-item {
          margin-bottom: 30px;

          &::after {
            content: '';
            display: block;
            width: 2px;
            height: 60px;
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            background-color: #e9e9e9;
          }
        }
      }

      .how-it-works__table-img {
        max-width: 270px;
        min-height: 184px;
        margin-bottom: 75px;
        order: 1;
        z-index: 15;
      }

      .how-it-works__table-box {
        margin-bottom: 45px;
      }

      .how-it-works__table-box.top,
      .how-it-works__table-box.bottom {
        max-width: 270px;
        width: 100%;
        text-align: center;

        &::after {
          content: '';
          display: block;
          width: 2px;
          height: 30px;
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          background-color: #e9e9e9;
        }
      }

      .how-it-works__table-box.office {
        margin-bottom: 10px;
        order: 2;

        .how-it-works__table-item {
          &:nth-child(2),
          &:nth-child(3) {
            display: flex;
          }
        }

        &::after {
          content: '';
          display: block;
          width: 2px;
          height: 70px;
          position: absolute;
          bottom: calc(100% + 15px);
          left: 50%;
          transform: translateX(-50%);
          background-color: #e9e9e9;
        }
      }

      .how-it-works__table-box.communication {
        order: 5;
        margin-bottom: 20px;
        .how-it-works__table-item {
          &:nth-child(2),
          &:nth-child(3) {
            display: flex;
          }
        }
      }

      .how-it-works__table-box.top {
        order: 3;
        z-index: 8;
        &::after {
          height: 60px;
        }

        &::before {
          content: '';
          display: block;
          width: 2px;
          height: 90px !important;
          position: absolute;
          bottom: 100%;
          left: 50%;
          transform: translateX(-50%);
          background-color: #e9e9e9;
        }
      }

      .how-it-works__table-box.left {
        order: 4;

        &::after {
          content: '';
          display: block;
          width: 2px;
          height: 60px;
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          background-color: #e9e9e9;
        }
      }

      .how-it-works__table-box.bottom {
        order: 4;
      }

      .how-it-works__table-box.top-right {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 140px;
        width: 100%;
        order: 6;
      }

      .line-h,
      .line-v {
        display: none;
      }
    }
  }
}

.how-it-works.type-3 {
  .how-it-works__table-box.office .how-it-works__table-list .line-v {
    height: calc(100% - 89px);
    top: calc(50% - -2px);
  }
}

@keyframes opacity {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.diagram {
  &__container {
    margin: 0 auto;
    max-width: 1470px;
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
  }
  &__body {
    display: flex;
    flex-direction: column;
    gap: 40px;
    padding-top: 80px;
    padding-bottom: 50px;
  }
}
