.about-company {
  // .about-company__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .about-company__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 36px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .about-company__bottom

  &__bottom {
    display: flex;
    flex-direction: column;
  }

  // .about-company__top-item

  &__top-items {
    display: flex;
    align-items: center;
    gap: 31px;
    margin-bottom: 20px;
  }

  // .about-company__top-left

  &__top-left {
  }

  // .about-company__top-right

  &__top-right {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  // .about-company__item

  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;
  }

  // .about-company__icons

  &__icons {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    @media (max-width: 1400px) {
      display: none;
    }
  }

  // .about-company__icon-top

  &__icon-top {
    border-radius: 10px;
    border: 1px solid var(--light-gray, #e9e9e9);
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    padding: 20px;
    width: 180px;
    min-height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 1600px) {
      width: 140px;
      img {
        width: 80px;
      }
    }
  }

  // .about-company__icon-bottom

  &__icon-bottom {
    border-radius: 10px;
    border: 1px solid var(--light-gray, #e9e9e9);
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    padding: 20px;
    width: 180px;
    min-height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 1600px) {
      width: 140px;
      img {
        width: 80px;
      }
    }
  }

  // .about-company__info-top

  &__info-top {
    display: flex;
    min-height: 180px;
    width: 735px;
    padding: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 12px;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    @media (max-width: 1600px) {
      width: 660px;
    }
  }

  // .about-company__info-top-title

  &__info-top-title {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%; /* 30px */
    @media (max-width: 1600px) {
      font-size: 16px;
    }
  }

  // .about-company__info-top-list

  &__info-top-columns {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__info-top-column {
    display: flex;
    flex-direction: column;
    gap: 2px;
  }

  &__info-top-list {
    li {
      color: #353535;
      font-family: Inter;
      font-size: 16px;
      font-style: normal;
      font-weight: 300;
      line-height: 150%;
      align-items: center;
      display: flex;
      white-space: nowrap;
      gap: 12px;
      @media (max-width: 1600px) {
        font-size: 13px;
      }
      span {
        border-radius: 4px;
        width: 24px;
        height: 24px;
        background: #95dbfa;
        @media (max-width: 1600px) {
          width: 18px;
          height: 18px;
        }
      }
    }
  }

  // .about-company__info-bottom

  &__info-bottom {
    display: flex;
    min-height: 180px;
    width: 735px;
    padding: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    @media (max-width: 1600px) {
      width: 660px;
    }
    h3 {
      color: #353535;
      text-align: center;
      font-family: Inter;
      font-size: 20px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%;
      max-width: 438px;
      @media (max-width: 1600px) {
        font-size: 16px;
      }
    }
  }

  // .about-company__calls

  &__calls {
    display: flex;
    flex-direction: column;
    gap: 12px;
    min-width: 226px;
    justify-content: center;
    align-items: center;
    min-height: 380px;
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    padding: 40px;
    @media (max-width: 1600px) {
      min-width: unset;
      width: 180px;
    }
  }

  // .about-company__calls-icon

  &__calls-icon {
    @media (max-width: 1600px) {
      width: 70px;
    }
  }

  // .about-company__calls-count

  &__calls-count {
    color: #007bb3;
    text-align: center;
    font-family: Inter;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%;
    @media (max-width: 1600px) {
      font-size: 26px;
    }
  }

  // .about-company__calls-title

  &__calls-title {
    color: #03a9f4;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%;
    text-transform: uppercase;
    @media (max-width: 1600px) {
      font-size: 16px;
    }
  }

  // .about-company__calls-subtitle

  &__calls-subtitle {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-weight: 300;
    line-height: 150%;
    text-align: center;
    max-width: 145px;
    margin: 0 auto;
    @media (max-width: 1600px) {
      font-size: 12px;
    }
    span {
      font-weight: 500;
    }
  }

  // .about-company_span

  &_span {
  }

  // .about-company__bottom-item

  &__bottom-item {
    padding-bottom: 28px;
  }

  // .about-company__projects

  &__projects {
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    padding: 20px 20px 28px 20px;
  }

  // .about-company__items

  &__items {
    display: flex;
    align-items: center;
    gap: 194px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 8%;
      top: 58%;
      transform: translate(0%, -50%);
      width: 84%;
      border: 3px dashed #95dbfa;
      @media (max-width: 1600px) {
        width: 86%;
      }
      @media (max-width: 1400px) {
        width: 88%;
        top: 56%;
      }
    }
  }

  // .about-company__item

  &__item {
    display: flex;
    flex-direction: column;
  }

  // .about-company__title

  &__title {
    color: #58595b;
    text-align: center;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%;
    flex-basis: 54px;
    &--actual {
      color: #007bb3;
    }
    @media (max-width: 1600px) {
      font-size: 20px;
    }
    @media (max-width: 1400px) {
      font-size: 16px;
    }
  }

  // .about-company__year

  &__year {
    color: #58595b;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 150%; /* 30px */
    text-transform: uppercase;
    padding: 2px 8px;
    border-radius: 4px;
    background: #e9e9e9;
    flex-basis: 34px;
    @media (max-width: 1600px) {
      font-size: 16px;
    }
    &--actual {
      background: #ccedfb;
    }
    @media (max-width: 1400px) {
      font-size: 13px;
      flex-basis: 10px;
    }
  }

  // .about-company__subititle

  &__subititle {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 150%; /* 24px */
    max-width: 300px;
    flex-basis: 72px;
    @media (max-width: 1720px) {
      font-size: 14px;
      max-width: 350px;
    }
    @media (max-width: 1600px) {
      font-size: 12px;
    }
    @media (max-width: 1400px) {
      font-size: 9px;
    }
  }
  &__span {
    width: 24px;
    height: 24px;
    border-radius: 4px;
    background: #95dbfa;
  }
}
