.company {
  // .company__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  &__subititle {
    color: #007bb3;
    font-family: Inter;
    font-size: 38px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
    text-transform: lowercase;
    text-align: center;
  }

  // .company__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 100px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .company__bottom

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .company__left

  &__left {
    display: flex;
    flex-direction: column;
    gap: 24px;
    align-items: center;
    @media (max-width: 1600px) {
      align-items: start;
    }
  }

  // .company__left-top

  &__left-top {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 28px;
    font-weight: 600;
    line-height: 150%;
    max-width: 525px;
    span {
      color: rgba(0, 123, 179, 1);
    }
    @media (max-width: 1600px) {
      text-align: left;
    }
  }

  // .company__left-bottom

  &__left-bottom {
    @media (max-width: 1600px) {
      width: 100%;
    }
  }

  // .company__steps

  &__steps {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  // .company__step

  &__step {
    display: flex;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    border: 4px solid var(--medium-blue, #95dbfa);
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    padding: 24px;
  }

  // .company__step-title

  &__step-title {
    color: #03a9f4;
    font-family: Montserrat;
    font-size: 24px;
    font-weight: 700;
    line-height: 150%;
    text-transform: uppercase;
    @media (max-width: 1600px) {
      font-size: 16px;
    }
  }

  // .company__step-subitlte

  &__step-subitlte {
    color: #353535;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 756px;
    @media (max-width: 1600px) {
      font-size: 16px;
      max-width: 300px;
    }
  }

  // .company__right

  &__right {
    position: relative;
    @media (max-width: 1300px) {
      width: 50%;
    }
    &::after {
      content: '';
      width: 886px;
      height: 308px;
      border-radius: 10px 0px 0px 10px;
      background: #f8f8f8;
      position: absolute;
      left: -50%;
      top: 35%;
      z-index: -1;
      @media (max-width: 1300px) {
        width: 500px;
        right: 0;
        left: unset;
      }
    }
    &::before {
      content: '';
      width: 703px;
      height: 260px;
      position: absolute;
      top: 10%;
      left: -30%;
      border-radius: 10px;
      border: 1px solid #82d7f7;
      @media (max-width: 1300px) {
        width: 500px;
        right: 0;
        left: unset;
      }
    }
    img {
      position: relative;
      z-index: 20;
      width: 509.885px;
      height: 509.997px;
      @media (max-width: 1300px) {
        width: unset;
        height: unset;
      }
    }
  }
}
