.calls {
  // .calls__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
  }

  // .calls__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 137px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .calls__bottom

  &__bottom {
    max-width: 1800px;
    padding: 0 40px;
    margin: 0 auto;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translate(0%, -50%);
      left: 0;
      background: url(../svg/bg-img2.svg);
      width: 100%;
      height: 200px;
      z-index: -1;
    }
  }

  // .calls__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 40px;
    position: relative;
    @media (max-width: 1400px) {
      flex-direction: column;
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 1px solid rgba(3, 169, 244, 0.31);
      width: 780px;
      height: 500px;
      top: 50%;
      transform: translate(0, -50%);
      right: 10%;
      z-index: -2;
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 1px solid rgba(203, 203, 203, 0.49);
      width: 780px;
      height: 500px;
      top: 45%;
      transform: translate(0, -50%);
      left: 9%;
      z-index: -2;
    }
  }

  // .calls__card

  &__card {
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: center;
    justify-content: center;
    min-height: 340px;
    width: 304px;
    &:nth-child(even) {
      width: 304px;
      height: 388px;
    }
    @media (max-width: 1800px) {
      max-width: 304px;
      width: 100%;
      &:nth-child(even) {
        max-width: 304px;
        width: 100%;
        height: auto;
      }
    }
    @media (max-width: 1400px) {
      max-width: unset;
      width: 100%;
      &:nth-child(even) {
        max-width: unset;
        width: 100%;
      }
    }
  }

  // .calls__img

  &__img {
    img {
      width: 70px;
      height: 70px;
    }
  }

  // .calls__card-title

  &__card-title {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 194px;
    @media (max-width: 1400px) {
      max-width: unset;
    }
  }

  // .calls__list

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px;
    li {
      color: #353535;
      font-family: Inter;
      font-size: 16px;
      font-weight: 300;
      line-height: 150%;
      position: relative;
      display: flex;

      align-items: center;
      gap: 12px;
      &::before {
        content: '•';
      }
    }
  }
}
