.main-title {
  color: #58595b;
  font-family: Montserrat;
  font-size: 72px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  text-transform: uppercase;
  text-transform: uppercase;
  max-width: 1393px;
  text-align: left;

  span {
    color: rgba(3, 169, 244, 1);
  }
  @media (max-width: 1400px) {
    font-size: 60px;
  }
}

.main-subtitle {
  color: #353535;
  text-align: center;
  font-family: Inter;
  font-size: 28px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
}

.secondary-title {
  color: #000;
  text-align: center;
  font-family: Inter;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  @media (max-width: 1300px) {
    font-size: 25px;
  }
}

img{
  max-width: 100%;
}