.division {
  // .division__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  &__bottom-wrapper {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: -3%;
      top: 50%;
      transform: translate(0%, -50%);
      border-radius: 0px 10px 10px 0px;
      background: #ddf3fd;
      width: 80px;
      height: 544px;
    }
    &::before {
      content: '';
      position: absolute;
      right: -3%;
      top: 50%;
      transform: translate(0%, -50%);
      border-radius: 0px 10px 10px 0px;
      background: #ddf3fd;
      width: 80px;
      height: 544px;
    }
  }

  // .division__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 54px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .division__bottom

  &__bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 544px;
    position: relative;
    padding: 0 240px;
    &::after {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 4px dashed #95dbfa;
      width: 828px;
      height: 378px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 1px solid #e5e5e5;
      width: 1228px;
      height: 464px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }

  // .division__cards

  &__cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__center-block {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 40px;
    border-radius: 10px;
    background: #007bb3;
    color: #fff;
    text-align: center;
    font-family: Montserrat;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
  }

  // .division__card

  &__card {
    width: 494px;
    min-height: 172px;
    padding: 50px;
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    display: flex;
    align-items: center;
    gap: 20px;
    @media (max-width: 1600px) {
      width: 400px;
    }
    @media (max-width: 1300px) {
      width: 300px;
      padding: 10px;
    }
  }

  // .division__img

  &__img {
    img {
      width: 70px;
      height: 70px;
    }
  }

  // .division__card-title

  &__card-title {
    color: #353535;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 303px;
    @media (max-width: 1600px) {
      font-size: 18px;
      max-width: 200px;
    }
    @media (max-width: 1300px) {
      font-size: 16px;
      max-width: 150px;
    }
  }
}
