.radio {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  min-width: 15px;
  height: 15px;
  position: relative;
  border: 1px solid #000;
  transition: all 0.3s ease;
  cursor: pointer;
  border-radius: 50%;
}
.radio:hover .checkbox__custom {
  opacity: 0.2;
}
.radio__input {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  appearance: none;
  cursor: pointer;
  border-radius: inherit;
}
.radio__custom {
  width: 100%;
  height: 100%;
  transition: all 0.3s ease;
  border: 1px solid #fff;
  pointer-events: none;
  background-color: #000;
  opacity: 0;
  border-radius: inherit;
}
.radio__input:checked + .radio__custom {
  opacity: 1;
}

.radio-group__title {
  font-size: 18px;
  margin-bottom: 15px;
  transition: all 0.3s ease;
}
.radio-group__item {
  display: flex;
  align-items: flex-start;
  width: fit-content;
  cursor: pointer;
}
.radio-group__item:hover .radio-group__item-name {
  color: #ca66fb;
}
.radio-group__item:not(:last-child) {
  margin-bottom: 10px;
}
.radio-group__item .radio {
  margin-right: 10px;
}
.radio-group__item-name {
  transition: all 0.3s ease;
}

.checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  min-width: 20px;
  height: 20px;
  position: relative;
  border: 1px solid #000;
  transition: all 0.3s ease;
  cursor: pointer;
}
.checkbox:hover .checkbox__custom {
  opacity: 0.2;
}
.checkbox__input {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  appearance: none;
  cursor: pointer;
}
.checkbox__custom {
  width: 100%;
  height: 100%;
  transition: all 0.3s ease;
  border: 1px solid #fff;
  pointer-events: none;
  background-color: #000;
  opacity: 0;
}
.checkbox__input:checked + .checkbox__custom {
  opacity: 1;
}

.field {
  width: 100%;
}
.field.invalid .radio-group__title {
  color: red;
}
.field.invalid .radio-group__item-name {
  color: red;
}
.field.invalid .field__textarea-container .icon, .field.invalid .field__input-container .icon {
  fill: red;
}
.field.invalid .field__textarea, .field.invalid .field__input {
  border-color: red;
}
.field.invalid .field__error {
  display: block;
}
.field.valid .field__textarea-container .icon, .field.valid .field__input-container .icon {
  fill: green;
}
.field.valid .field__textarea, .field.valid .field__input {
  border-color: green;
  color: green;
}
.field__head {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.field__title {
  display: block;
  margin-bottom: 5px;
}
.field__optional {
  display: block;
  margin-left: 10px;
  color: grey;
}
.field__textarea-container, .field__input-container {
  width: 100%;
  position: relative;
  transition: all 0.3s ease;
}
.field__input-container {
  width: 100%;
  margin-bottom: 5px;
  position: relative;
}
.field__input-container .icon {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}
.field__input-container .icon + .field__input {
  padding-left: 35px;
}
.field__textarea-container .icon {
  position: absolute;
  top: 10px;
  left: 10px;
}
.field__textarea-container .icon + .field__textarea {
  padding-left: 35px;
}
.field__textarea, .field__input {
  width: 100%;
  padding: 10px 20px;
  text-align: start;
  border: 1px solid #000;
}
.field__input {
  min-height: 43px;
}
.field__input::placeholder {
  color: grey;
}
.field__textarea {
  min-height: 150px;
}
.field__error, .field__message {
  display: none;
  margin-top: 5px;
  width: 100%;
}
.field__error {
  color: red;
}

.form .row {
  flex-wrap: wrap;
}
.form .col {
  width: 50%;
