.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.modal.show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.modal__dialog {
  max-width: 600px;
  width: 100%;
  position: relative;
  background-color: #fff;
}
.modal__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 20px 10px;
}
.modal__header-close-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 30px;
  min-width: 30px;
  height: 30px;
}
.modal__body {
  padding: 10px 20px 30px;
}

.select {
  width: fit-content;
  min-width: 150px;
  max-width: 200px;
  position: relative;
  margin: 0 auto;
}
.select.open-top .select__dropdown {
  top: unset;
  bottom: calc(100% + 10px);
  transform: translateY(30px);
}
.select.active {
  z-index: 100;
}
.select.active .select__trigger .icon-arrow-select {
  transform: rotate(-180deg);
}
.select.active .select__dropdown {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}
.select__trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 5px 15px;
  text-align: start;
  position: relative;
  z-index: 3;
  border: 1px solid #000;
  background-color: var(--baseWhite);
  cursor: pointer;
  transition: all 0.3s ease;
}
.select__trigger:focus-within {
  box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.2);
}
.select__trigger-value {
  display: block;
  width: 100%;
  -webkit-line-clamp: 1;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.select__trigger .icon {
  margin-left: 10px;
}
.select__dropdown {
  width: 100%;
  height: 150px;
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  z-index: 2;
  background-color: var(--baseWhite);
  border: 1px solid #000;
  transition: all 0.3s ease;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-30px);
}
.select__list {
  max-height: 100%;
  overflow-y: auto;
}
.select__link, .select__btn {
  display: block;
  width: 100%;
  padding: 4px 10px;
  text-align: start;
  cursor: pointer;
  transition: all 0.3s ease;
}
.select__link:hover, .select__btn:hover {
  background: var(--baseBlack);
  color: var(--baseWhite);
}

