  border: 4px solid #95dbfa;
  background: #fff;
  box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
  padding: 20px 20px 28px 20px;
}
.about-company__items {
  display: flex;
  align-items: center;
  gap: 194px;
  position: relative;
}
.about-company__items::after {
  content: "";
  position: absolute;
  left: 8%;
  top: 58%;
  transform: translate(0%, -50%);
  width: 84%;
  border: 3px dashed #95dbfa;
}
@media (max-width: 1600px) {
  .about-company__items::after {
    width: 86%;
  }
}
@media (max-width: 1400px) {
  .about-company__items::after {
    width: 88%;
    top: 56%;
  }
}
.about-company__item {
  display: flex;
  flex-direction: column;
}
.about-company__title {
  color: #58595b;
  text-align: center;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  flex-basis: 54px;
}
.about-company__title--actual {
  color: #007bb3;
}
@media (max-width: 1600px) {
  .about-company__title {
    font-size: 20px;
  }
}
@media (max-width: 1400px) {
  .about-company__title {
    font-size: 16px;
  }
}
.about-company__year {
  color: #58595b;
  text-align: center;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%; /* 30px */
  text-transform: uppercase;
  padding: 2px 8px;
  border-radius: 4px;
  background: #e9e9e9;
  flex-basis: 34px;
}
@media (max-width: 1600px) {
  .about-company__year {
    font-size: 16px;
  }
}
.about-company__year--actual {
  background: #ccedfb;
}
@media (max-width: 1400px) {
  .about-company__year {
    font-size: 13px;
    flex-basis: 10px;
  }
}
.about-company__subititle {
  color: #353535;
  text-align: center;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 150%; /* 24px */
  max-width: 300px;
  flex-basis: 72px;
}
@media (max-width: 1720px) {
  .about-company__subititle {
    font-size: 14px;
    max-width: 350px;
  }
}
@media (max-width: 1600px) {
  .about-company__subititle {
    font-size: 12px;
  }
}
@media (max-width: 1400px) {
  .about-company__subititle {
    font-size: 9px;
  }
}
.about-company__span {
  width: 24px;
  height: 24px;
  border-radius: 4px;
  background: #95dbfa;
}

.clients__container {
