.footer__container {
  max-width: 1429px;
  margin: 0 auto;
  width: 100%;
  padding: 0 15px;
}
.footer__body {
  display: flex;
  align-items: center;
  padding-top: 139px;
  justify-content: space-between;
  padding-bottom: 50px;
}
.footer__left {
  display: flex;
  flex-direction: column;
  gap: 80px;
  position: relative;
}
.footer__left::after {
  content: "";
  position: absolute;
  border-radius: 0px 0px 10px 0px;
  background: rgba(221, 243, 253, 0.48);
  width: 620px;
  height: 498px;
  left: -50%;
  top: -25%;
  z-index: -1;
}
.footer__left::before {
  content: "";
  position: absolute;
  width: 1228px;
  height: 562px;
  border-radius: 10px;
  border: 1px solid #e5e5e5;
  left: -100%;
  top: -12%;
  z-index: -2;
}
.footer__title {
  color: #58595b;
  text-align: center;
  font-family: Inter;
  font-size: 60px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
@media (max-width: 1300px) {
  .footer__title {
    font-size: 40px;
  }
}
.footer__title-price {
  color: #007bb3;
  text-transform: uppercase;
  font-size: 60px;
  font-weight: 800;
}
@media (max-width: 1300px) {
  .footer__title-price {
    font-size: 40px;
  }
}
.footer__title-bluebg {
  font-size: 32px;
  border-radius: 10px;
  background: #40bdf6;
  padding: 8px 20px;
  color: #fff;
}
.footer__title-spanblue {
  color: #00aeef;
}
.footer__info {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.footer__info-item {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.footer__subtitle {
  color: #58595b;
  text-align: center;
  font-family: Inter;
  font-size: 24px;
  max-width: 613px;
}
.footer__subtitle span {
  color: #00aeef;
  font-weight: 600;
}
.footer__tel {
  border-radius: 10px;
  border: 1px solid #95dbfa;
  background: #fff;
  display: flex;
  padding: 20px;
  align-items: center;
  gap: 12px;
  max-width: 500px;
  margin: 0 auto;
}
.footer__tel a,
.footer__tel span {
  color: #58595b;
  text-align: center;
  font-family: Inter;
  font-size: 28px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
@media (max-width: 1300px) {
  .footer__tel a,
  .footer__tel span {
    font-size: 16px;
  }
}
.footer__right {
  position: relative;
}
.footer__right::after {
  content: "";
  position: absolute;
  width: 710px;
  height: 494px;
  border-radius: 10px;
  border: 1px solid #82d7f7;
  z-index: -1;
  right: -50%;
  top: 25%;
}
.footer__right img {
  width: 599.999px;
  height: 600px;
}
@media (max-width: 1300px) {
  .footer__right img {
    width: 450px;
    height: auto;
  }
}
.footer__mail {
  max-width: 313px;
}

.footer-phone,
.footer-mail {
  width: 34px;
  height: 34px;
  fill: none;
  stroke: rgb(0, 174, 239);
  stroke-width: 3;
}
@media (max-width: 1300px) {
  .footer-phone,
  .footer-mail {
    width: 20px;
    height: 20px;
  }
}

