.call-staticks {
  // .call-staticks__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
  }

  // .call-staticks__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 40px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .call-staticks__bottom

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 1px solid rgba(3, 169, 244, 0.31);
      width: 1020px;
      height: 353px;
      top: 40%;
      -webkit-transform: translate(0, -50%);
      transform: translate(0, -50%);
      right: -1%;
      z-index: -2;
    }
  }

  // .call-staticks__left

  &__left {
  }

  // .call-staticks__left-title

  &__left-title {
    color: #353535;
    font-family: Inter;
    font-size: 28px;
    font-weight: 600;
    line-height: 150%;
    padding: 0 35px;
    span {
      color: rgba(0, 123, 179, 1);
    }
    @media (max-width: 1600px) {
      text-align: center;
    }
  }

  // .call-staticks__cards

  &__cards {
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
  }

  // .call-staticks__card

  &__btn{
    
  }

  &__card {
    padding: 25px;
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    max-width: 493px;
    min-height: 172px;
    display: flex;
    align-items: center;
    width: 100%;
    // @media (max-width: 1720px) {
    // }
    @media (max-width: 1600px) {
      max-width: 410px;
    }
    @media (max-width: 1300px) {
      max-width: 265px;
      padding: 10px;
      justify-content: center;
      min-height: 290px;
    }
  }

  // .call-staticks__flex

  &__flex {
    display: flex;
    align-items: center;
    gap: 25px;
    @media (max-width: 1300px) {
      flex-direction: column;
    }
  }

  // .call-staticks__card-img

  &__card-img {
    img {
      width: 70px;
      height: 70px;
    }
  }

  // .call-staticks__card-title

  &__card-title {
    color: #353535;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    @media (max-width: 1300px) {
      font-size: 16px;
    }
  }

  // .call-staticks__right

  &__right {
  }

  // .call-staticks__img

  &__img {
    img {
      width: 525px;
      height: 558.71px;
      max-width: unset;
    }
  }

  // .call-staticks__card-list

  &__card-list {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-left: 15px;
    li {
      color: rgba(53, 53, 53, 1);
      font-family: Inter;
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      display: flex;
      align-items: center;
      gap: 5px;
      &::before {
        content: '•';
        color: rgba(0, 123, 179, 1);
        font-weight: 800;
      }
      @media (max-width: 1300px) {
        font-size: 16px;
      }
    }
  }
}
