.advances {
  // .advances__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .advances__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 117px;
    padding-top: 80px;
    padding-bottom: 50px;
  }

  // .advances__bottom

  &__bottom {
    position: relative;
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 50%;
      height: 2px;
      z-index: -2;
      content: '';
      border: 3px dashed #95dbfa;
    }
  }

  // .advances__cards

  &__cards {
    display: flex;
    align-items: center;
    gap: 160px;
    justify-content: center;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 10px;
      background: #f8f8f8;
      width: 1354px;
      height: 280px;
      z-index: -2;
    }
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 10px;
      border: 1px solid #95dbfa;
      width: 1354px;
      height: 328px;
      z-index: -2;
    }
  }

  // .advances__card

  &__card {
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    padding: 20px;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 10px;
      background: #ddf3fd;
      width: 460px;
      height: 100%;
      z-index: -1;
      @media (max-width: 1600px) {
        width: 400px;
      }
    }
  }

  // .advances__top

  &__card-top {
    margin-bottom: 5px;
    img {
      width: 70px;
      height: 70px;
    }
  }

  // .advances__center

  &__card-center {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 20px;
    font-weight: 500;
    line-height: 150%;
    max-width: 295px;
    @media (max-width: 1600px) {
      font-size: 16px;
    }
  }
  &__card-bottom {
    color: #353535;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    line-height: 150%;
    max-width: 340px;
    @media (max-width: 1600px) {
      max-width: 250px;
      font-size: 14px;
    }
    @media (max-width: 1300px) {
      font-size: 12px;
    }
  }
}
