.detailing {
  // .detailing__list

  &__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__left {
    @media (max-width: 1600px) {
      max-width: 215px;
    }
  }

  // .detailing__list-item

  &__list-item,
  &__second-list-item {
    color: #007bb3;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    &::before {
      content: '•';
    }
    @media (max-width: 1720px) {
      font-size: 12px;
    }
  }

  // .detailing__second-list

  &__second-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__left-text {
    color: #007bb3;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.14px;
    @media (max-width: 1720px) {
      font-size: 12px;
    }
  }
  &__left-wrapper {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  &__left-line1 {
    position: absolute;
    right: -30%;
    top: 44%;
    @media (max-width: 1720px) {
      right: -25%;
      top: 51%;
      svg {
        width: 90px;
      }
    }
    @media (max-width: 1600px) {
      top: 34%;
      right: -35%;
      svg {
        width: 80px;
      }
    }
    @media (max-width: 1400px) {
      top: 44%;
      right: -35%;
    }
    @media (max-width: 1300px) {
      top: 42%;
      right: -31%;

      svg {
        width: 70px;
      }
    }
  }
  &__left-line2 {
    position: absolute;
    left: -48%;
    top: 32%;
    @media (max-width: 1720px) {
      svg {
        width: 141px;
      }
    }
    @media (max-width: 1600px) {
      svg {
        width: 120px;
      }
    }
    @media (max-width: 1400px) {
      top: 46%;
      svg {
        width: 104px;
      }
    }
    @media (max-width: 1300px) {
      top: 37%;
      left: -51%;
    }
  }
  &__right {
    top: 9%;
  }
}
