.webphone {
  // .webphone__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .webphone__body

  &__body {
    padding-top: 80px;
    display: flex;
    flex-direction: column;
    gap: 108px;
    padding-bottom: 50px;
    @media (max-width: 1300px) {
      gap: 168px;
    }
  }

  // .webphone__bottom

  &__bottom {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      bottom: -29%;
      left: 11%;
      width: 641px;
      height: 384px;
      border-radius: 10px;
      background: #f8f8f8;
      z-index: -1;
      @media (max-width: 1600px) {
        bottom: -10px;
      }
      @media (max-width: 1300px) {
        height: 300px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      bottom: -29%;
      right: 11%;
      width: 641px;
      height: 384px;
      border-radius: 10px;
      background: #f8f8f8;
      z-index: -1;
      @media (max-width: 1600px) {
        bottom: 10px;
      }
      @media (max-width: 1300px) {
        height: 300px;
      }
    }
  }

  // .webphone__cards

  &__cards {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 385px;
    gap: 20px;
    position: relative;
    z-index: 2;
    @media (max-width: 1720px) {
      padding: 0 100px;
      justify-content: center;
    }
    &::after {
      position: absolute;
      content: '';
      width: 641px;
      height: 384px;
      right: 8%;
      bottom: -36%;
      border-radius: 10px;
      border: 1px solid #95dbfa;
      z-index: -1;
      @media (max-width: 1600px) {
        bottom: -10px;
      }
      @media (max-width: 1300px) {
        height: 300px;
      }
    }
    &::before {
      position: absolute;
      content: '';
      width: 641px;
      height: 384px;
      left: 8%;
      bottom: -36%;
      border-radius: 10px;
      border: 1px solid #95dbfa;
      z-index: -1;
      @media (max-width: 1600px) {
        bottom: 10px;
      }
      @media (max-width: 1300px) {
        height: 300px;
      }
    }
  }

  // .webphone__card

  &__card {
    display: flex;
    min-height: 172px;
    width: 514px;
    align-items: center;
    gap: 20px;
    padding: 50px 25px;
    border-radius: 10px;
    border: 1px solid var(--light-gray, #e9e9e9);
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    @media (max-width: 1300px) {
      width: 400px;
      height: 100px;
    }
  }

  // .webphone__img

  &__img {
    img {
      width: 70px;
      height: 70px;
      @media (max-width: 1400px) {
        width: 50px;
        height: 50px;
      }
    }
  }

  // .webphone__title

  &__title {
    color: #353535;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 374px;
    @media (max-width: 1400px) {
      font-size: 16px;
    }
  }
}

.web-phone-second {
  &__center {
  }
  &__list-item {
    display: flex;
    align-items: center;
    color: #007bb3;
    font-family: Inter;
    font-size: 14px;

    img {
      width: 28px;
      height: 28px;
    }
  }
  &__line1 {
    position: absolute;
    top: 62%;
    right: -33%;
    transform: translate(0%, -50%);
    @media (max-width: 1720px) {
      top: 55%;
    }
    @media (max-width: 1600px) {
      top: 48%;
      right: -30%;
      svg {
        width: 80px;
      }
    }
    @media (max-width: 1300px) {
      top: 37%;
      svg {
        width: 65px;
      }
    }
  }
  &__line2 {
    position: absolute;
    top: 48%;
    left: -41%;
    @media (max-width: 1720px) {
      top: 46%;
      left: -44%;
    }
    @media (max-width: 1600px) {
      top: 35%;
      left: -49%;
    }
    @media (max-width: 1400px) {
      top: 31%;
      left: -60%;
    }
    @media (max-width: 1300px) {
      top: 22%;
      left: -71%;
    }
  }
  &__left {
    width: 286px;
    min-width: unset;
    top: 21px;
    @media (max-width: 1400px) {
      top: 69px;
    }
  }
  &__right {
  }
  &__right {
    top: 90px;
    position: relative;
  }
  &__left-text {
    &::before {
      content: '';
    }
  }
}
