.about {
  &::before {
    content: '';
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    background: linear-gradient(90deg, #03a9f4 0%, rgba(3, 169, 244, 0) 100%);
    height: 32px;
  }
  &::after {
    content: '';
    position: absolute;
    bottom: 0px;
    z-index: 20;
    left: 0;
    width: 100%;
    background: linear-gradient(-90deg, #03a9f4 0%, rgba(3, 169, 244, 0) 100%);
    height: 32px;
  }
  // .about__container

  &__container {
    max-width: 1920px;
    margin: 0 auto;
    position: relative;
    padding: 0 81px;
    width: 100%;
    @media (max-width: 1700px) {
      padding: 0 15px;
    }
  }

  // .about__body

  &__body {
    display: flex;
    gap: 80px;
    padding-bottom: 50px;
    padding-top: 125px;
    @media (max-width: 1700px) {
      align-items: center;
    }
  }

  // .about__left

  &__left {
    display: flex;
    flex-direction: column;
    gap: 80px;
  }

  // .about__logo

  &__logo {
    @media (max-width: 1700px) {
      width: 450px;
    }
  }

  // .about__text

  &__text {
    padding: 40px;
    display: flex;
    flex-direction: column;
    gap: 40px;
    background: #ddf3fd;
    border-radius: 10px;
    max-width: 764px;
  }

  // .about__text-title

  &__text-title {
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 600;
    line-height: 150%;
    font-size: 30px;
    span {
      font-size: 48px;
      color: #007bb3;
      font-weight: 800;
      @media (max-width: 1700px) {
        font-size: 40px;
      }
      @media (max-width: 1250px) {
        font-size: 37px;
      }
    }
    @media (max-width: 1700px) {
      font-size: 26px;
    }
  }

  // .about__text-subtitle

  &__text-subtitle {
    color: #353535;
    font-size: 24px;
    font-weight: 300;
    line-height: 150%;
    @media (max-width: 1700px) {
      font-size: 20px;
    }
    @media (max-width: 1250px) {
      font-size: 17px;
    }
  }

  // .about__right

  &__right {
    padding: 100px 179px 208px;
    padding-right: 135px;

    border-radius: 10px;
    position: relative;
    height: 510px;
    @media (max-width: 1700px) {
      padding-right: unset;
      height: auto;
      padding: 85px;
    }
    img {
      z-index: 20;
      position: relative;
      width: 525px;
      height: 543.747px;
      @media (max-width: 1700px) {
        width: unset;
        height: unset;
      }
    }
    &::before {
      content: '';
      position: absolute;
      bottom: -50%;
      left: 41px;
      background: url(../svg/bg-img.svg);
      max-width: 542px;
      max-height: 542px;
      z-index: -1;
      width: 100%;
      height: 100%;
      @media (max-width: 1700px) {
        bottom: -4%;
        left: 0%;
        width: 75%;
        height: 75%;
      }
    }
    &::after {
      content: '';
      position: absolute;
      top: -6%;
      right: -30%;
      background: url(../svg/bg-img2.svg);
      max-width: 542px;
      max-height: 542px;
      width: 100%;
      height: 100%;
      z-index: -1;
      @media (max-width: 1700px) {
        // bottom: 0;
        // left: -5%;
        width: 75%;
        height: 75%;
      }
    }
  }

  &__line1 {
    position: absolute;
    border-radius: 10px;
    border: 2px solid rgba(189, 189, 189, 0.64);
    width: 77%;
    height: 100%;
    top: 48%;
    left: 32%;
    z-index: -2;
    @media (max-width: 1700px) {
      width: 85%;
      top: 57%;
      left: 13%;
    }
  }
  &__line2 {
    position: absolute;
    border-radius: 10px;
    border: 2px solid rgba(3, 169, 244, 0.31);
    width: 96%;
    height: 77%;
    top: -17%;
    right: -6%;
    z-index: -2;
    @media (max-width: 1700px) {
      top: -36%;
      right: 0%;
    }
  }

  &__line4 {
    position: absolute;
    border-radius: 10px;
    border: 2px solid rgba(189, 189, 189, 0.62);
    width: 100%;
    height: 100%;
    top: 69%;
    right: -83.5%;
    z-index: -2;
  }
  &__line5 {
    @media (max-width: 1700px) {
      position: absolute;
      border-radius: 10px;
      border: 2px solid rgba(3, 169, 244, 0.31);
      width: 100%;
      height: 114%;
      left: -5%;
      z-index: -2;
      bottom: -13%;
    }
  }
  &__whiteline1 {
    position: absolute;
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.62);
    width: 100%;
    height: 100%;
    top: 11%;
    right: -62.5%;
    z-index: 1;
    @media (max-width: 1700px) {
      right: -51.5%;
    }
  }
  &__whiteline2 {
    position: absolute;
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.62);
    width: 100%;
    height: 100%;
    top: 31%;
    right: -93.5%;
    z-index: 1;
    @media (max-width: 1700px) {
      right: -91.5%;
    }
  }
}
