.autocall-company {
  // .autocall-company__line1

  &__line1 {
    position: absolute;
    right: -30%;
    @media (max-width: 1720px) {
      right: -29%;
      svg {
        width: 74px;
      }
    }
    @media (max-width: 1600px) {
      svg {
        width: 69px;
      }
    }
    @media (max-width: 1400px) {
      right: -37%;
      top: 6%;
    }
    @media (max-width: 1300px) {
      right: -45%;
      top: 5%;
    }
  }

  // .autocall-company__line2

  &__line2 {
    position: absolute;
    left: -273%;
    top: 49%;
    transform: translate(0%, -50%);
    @media (max-width: 1720px) {
      left: -264%;
      svg {
        width: 814px;
      }
    }
    @media (max-width: 1600px) {
      svg {
        width: 726px;
      }
    }
    @media (max-width: 1400px) {
      svg {
        width: 671px;
      }
    }
    @media (max-width: 1300px) {
      svg {
        width: 593px;
      }
    }
    &::after {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      left: 0px;
      bottom: 1px;
    }
    &::before {
      content: '';
      position: absolute;
      border-radius: 50%;
      background: #00aeef;
      width: 12px;
      height: 12px;
      right: -4px;
      bottom: 1px;
    }
  }
  &__left {
    top: 175px;
    @media (max-width: 1720px) {
      top: 168px;
    }
    @media (max-width: 1600px) {
      top: 157px;
    }
  }
  &__right {
    top: 260px;
    position: relative;
    left: 28px;
    @media (max-width: 1720px) {
      top: 271px;
      left: 0;
    }
    @media (max-width: 1600px) {
      top: 220px;
    }
    @media (max-width: 1300px) {
      top: 196px;
    }
  }
  &__left-list-item {
    @media (max-width: 1400px) {
      gap: 5px;
    }
  }
  &__left-text {
    @media (max-width: 1720px) {
      max-width: 121px;
    }
    @media (max-width: 1300px) {
      max-width: 85px;
    }
  }
}

