.panel {
  // .panel__container

  &__container {
  }

  // .panel__body

  &__body {
    padding-top: 80px;
    display: flex;
    flex-direction: column;
    gap: 76px;
  }

  // .panel__bottom

  &__bottom {
    display: flex;
    align-items: center;
    gap: 133px;
    @media (max-width: 1700px) {
      padding-bottom: 80px;
    }
  }

  // .panel__left

  &__left {
  }

  // .panel__right

  &__right {
    max-width: 1064px;
  }

  // .panel__cards

  &__cards {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      border-radius: 10px;
      border: 4px dashed #95dbfa;
      width: 90%;
      height: 54%;
      top: 27%;
      left: 50%;
      -webkit-transform: translate(-50%);
      transform: translate(-50%);
      z-index: -1;
    }
  }

  // .panel__card

  &__card {
    padding: 20px 16.5px;
    border-radius: 10px;
    border: 4px solid #95dbfa;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    display: flex;
    flex-direction: column;
    gap: 25px;
    width: 251px;
    min-height: 260px;
    @media (max-width: 1500px) {
      width: 200px;
      min-height: 220px;
    }
    @media (max-width: 1350px) {
      width: 170px;
    }
  }

  // .panel__img

  &__img {
    margin: 0 auto;
    svg {
      width: 70px;
      height: 70px;
      @media (max-width: 1500px) {
        width: 50px;
        height: 50px;
      }
    }
  }

  // .panel__title

  &__title {
    color: #353535;
    text-align: center;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
    max-width: 210px;
    @media (max-width: 1500px) {
      font-size: 16px;
    }
  }
}
