.system {
  // .system__container

  &__container {
    max-width: 1888px;
    margin: 0 auto;
    padding: 0 32px;
    width: 100%;
    position: relative;
  }

  // .system__body

  &__body {
    display: flex;
    flex-direction: column;
    gap: 97px;
    padding-bottom: 50px;
    padding-top: 80px;
  }

  // .system__bottom

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .system__left

  &__left {
  }

  // .system__right

  &__right {
  }

  // .system__right-wrapper

  &__right-wrapper {
    border-radius: 10px;
    background: #ddf3fd;
    padding: 24px;
    position: relative;
    &::after {
      content: '';
      border-radius: 10px;
      border: 1px solid rgba(0, 174, 239, 0.49);
      left: -46px;
      position: absolute;
      bottom: -10%;
      width: 888px;
      height: 383px;
      z-index: -1;
      @media (max-width: 1400px) {
        width: 80%;
      }
    }
    &::before {
      content: '';
      border-radius: 10px;
      border: 4px dashed #95dbfa;
      right: -46px;
      position: absolute;
      top: -10%;
      width: 888px;
      height: 383px;
      z-index: -1;
      @media (max-width: 1400px) {
        width: 80%;
      }
    }
    @media (max-width: 1400px) {
      width: 80%;
      margin-left: auto;
    }
  }

  // .system__columns

  &__columns {
    display: flex;
    align-items: center;
    gap: 12px;
  }

  // .system__column

  &__column {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  // .system__card

  &__card {
    display: flex;
    align-items: center;
    gap: 8px;
    border-radius: 10px;
    border: 1px solid #e9e9e9;
    background: #fff;
    box-shadow: 0px 4px 15px 0px rgba(88, 89, 91, 0.07);
    padding: 12px 6.5px;
    min-height: 81px;
  }

  // .system__img

  &__img {
    img {
      width: 63px;
      height: 63px;
      @media (max-width: 1400px) {
       width: auto;
       height: auto;
      }
    }
  }

  // .system__card-title

  &__card-title {
    color: #353535;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    max-width: 188px;
    @media (max-width: 1400px) {
      max-width: 150px;
      font-size: 14px;
     }
  }
}
