.script-one {
  &__left {
    top: 96px;
  }
  &__line1 {
    position: absolute;
    right: -30%;
    top: 47%;
    transform: translate(0, -50%);
    @media (max-width: 1720px) {
      right: -25%;
      top: 54%;
      svg {
        width: 87px;
      }
    }
    @media (max-width: 1600px) {
      top: 47%;
    }
    @media (max-width: 1400px) {
      top: 55%;
      right: -20%;
      svg {
        width: 69px;
      }
    }
  }
  &__line2 {
    position: absolute;
    left: -50%;
    top: 41%;
    transform: translate(0, -50%);
    @media (max-width: 1720px) {
      left: -47%;
      top: 23%;
      svg {
        width: 140px;
      }
    }
    @media (max-width: 1600px) {
      svg {
        width: 123px;
      }
    }
    @media (max-width: 1400px) {
      svg {
        width: 108px;
      }
    }
    @media (max-width: 1300px) {
      svg {
        width: 85px;
      }
    }
  }
  &__line3 {
    position: absolute;
    left: -59%;
    top: 36%;
    transform: translate(0, -50%);
    @media (max-width: 1720px) {
      left: -57%;
      width: 167px;
      top: 33%;
      svg{
        width: 166px;
      }
    }
    @media (max-width: 1600px) {
      svg {
        width: 123px;
      }
    }
    @media (max-width: 1400px) {
      svg {
        width: 128px;
      }
    }
    @media (max-width: 1300px) {
      svg {
        width: 102px;
      }
    }
  }
  &__right {
    top: 218px;
    position: relative;
  }
  &__right-items {
    gap: 268px;
    @media (max-width: 1720px) {
      gap: 209px;
    }
    @media (max-width: 1600px) {
      gap: 131px;
    }
    @media (max-width: 1400px) {
      gap: 131px;
    }
    @media (max-width: 1300px) {
      gap: 94px;
    }
  }
}

.script-two {
  &__container {
    padding: 0 15px;
  }
  &__right {
  }
  &__left {
    position: relative;
    bottom: -45px;
  }
  &__center {
    img {
      width: unset;
      height: unset;
    }
    @media (max-width: 1720px) {
      width: 80%;
    }
  }
  &__line1 {
    position: absolute;
    left: -32%;
    top: 50%;
    transform: translate(0%, -50%);
    @media (max-width: 1600px) {
      left: -28%;
      svg {
        width: 96px;
      }
    }
    @media (max-width: 1400px) {
      left: -33%;
    }
    @media (max-width: 1300px) {
      svg {
        width: 75px;
      }
    }
  }
  &__items {
    justify-content: unset;
    gap: 18px;
  }
}
